<template>
  <div class="bookitem">
    <el-row :gutter="30">
      <el-col v-show="bookLoad" :xs="24" :sm="24" :md="12" :lg="12" v-for="(item,index) in book" :key="index">
        <div class="grid-content book" :class="{'left':index%2==0,'right':(index-1)%2==0}">
          <div class="left_img">
            <img :src="item.img" width="150" height="200">
          </div>
          <div class="meg">
            <div class="title_book">
              <router-link :to="'/getOneBook/'+item._id">{{item.name}}</router-link>
            </div>
            <div class="state">
              <div class="name">{{item.actor}}</div>
              <div class="bar">|</div>
              <div class="category">{{item.category}}</div>
              <div class="bar">|</div>
              <div class="book_state">连载中</div>
            </div>
            <router-link :to="'/getOneBook/'+item._id">
              <div class="resume" v-html="item.brief"></div>
            </router-link>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        show: false
      }
    },
    computed: {
      book() {
        return this.$store.getters.getPageBook
      },
      bookLoad() {
        return this.$store.getters.bookLoading
      }
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  @keyframes left
    0%
      opacity: 0;
      transform: translateX(-30px)
    100%
      opacity: 1;
      transform: translateX(0)
  @keyframes right
    0%
      opacity: 0;
      transform: translateX(30px)
    100%
      opacity: 1;
      transform: translateX(0)
  .bookitem
    width: 80%;
    height: auto;
    padding-top: 20px;
    margin: 0 auto;
    .book
      display: flex;
      width: 100%;
      height: 240px;
      .left_img
        flex: 0 0 180px;
        overflow: hidden;
        & > img
          padding: 10px;
        &>img:hover
          transition: ease .2s;
          transform-origin:50% 50%;
          transform: scale(1.2);
      .meg
        flex: 1;
        .title_book
          text-align: center;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          margin-top: 10px;
          line-height: 30px;
          height: 30px;
          font-size: 24px;
          font-weight: bold;
          &>a
            text-decoration: none;
            color: #000;
          &>a:hover
            text-decoration: underline;
        .state
          line-height: 25px;
          height: 25px;
          overflow: hidden;
          .name, .bar, .category, .book_state
            float: left;
            margin: 0 5%;
        &>a
          text-decoration: none;
          color: #000;
        &>a:hover
          text-decoration: none;
          color: #ccc;
        .resume
          width: 95%;
          height: 150px;
          margin-top: 5px;
          overflow: hidden;
          text-overflow: ellipsis;
    .left
      animation: left .5s ease;
    .right
      animation: right .5s ease;
  @media screen and (max-width: 1200px)
    .bookitem
      width: 100%;
      padding-left: 10px;
</style>



