<template>
  <div>
    <el-table
      :data="tableData"
      style="width: 100%"
      v-loading="loading2"
      element-loading-text="拼命加载中"
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.8)"
      :default-sort="{prop: 'update', order: 'descending'}"
    >
      <el-table-column
        prop="update"
        label="更新日期"
        sortable
        width="180">
      </el-table-column>
      <el-table-column
        prop="category"
        label="分类"
        width="100">
      </el-table-column>
      <el-table-column
        prop="name"
        label="书名"
        sortable
        width="180">
      </el-table-column>
      <el-table-column
        prop="chapterNum"
        label="章节"
        sortable>
      </el-table-column>
      <el-table-column
        prop="title"
        label="章节名"
        width="180">
      </el-table-column>
      <el-table-column
        fixed="right"
        label="操作"
        width="50">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small">修改</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [],
        loading2: true
      }
    },
    created() {
      this.$http.get('/book/getUserChapter').then(res => {
        if (res.status == 200) {
          if (res.data.code === 0) {
            this.tableData = res.data.data.map(res => {
//              console.log(new Date(res.update))
              var oldtime = res.update
              var newDate = new Date();
              var time = new Date(oldtime).getTime()
              newDate.setTime(time)
              res.update = newDate.toLocaleString()
              return res
            })
            this.loading2 = false
          }
          if (res.data.code === 1) {
            this.$message.warning(res.data.meg)
          }
        }
      })
    },
    methods: {
      formatter(row, column) {
        return row.address;
      },
      handleClick (i) {
//        console.log(this.$store)
        this.$router.push('/fixchapter')
        this.$store.dispatch('getOneChapter', i)
      }
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
</style>



