<template>
  <div>
    <el-row class="warp">
      <el-col class="warp_nav" :lg="4" :md="6" :sm="8" :xs="8" style="height:510px">
        <transition name="fall">
          <div class="nav_down" v-show="navdown">
            <li v-for="(item,index) in category" :key="index" class="list_li" @click="changeTright(index)">
              <div class="text" :class="{'active': index === mark}">{{item.category}}</div>
              <div class="mark" :class="{'triangle-right': index===mark}"></div>
            </li>
          </div>
        </transition>
      </el-col>
      <el-col class="warp_car" :lg="20" :md="18" :sm="16" :xs="16" v-show="!navdown">
        <el-carousel class="carouses" trigger="click" height="510px" :interval="10000">
          <el-carousel-item v-for="(item,index) in list" :key="index">
            <img :src="item.img" width="100%" height="510px"/>
          </el-carousel-item>
        </el-carousel>
      </el-col>
      <el-col :lg="20" :md="18" :sm="16" :xs="16">
        <book-show :news="newBooks"
                   :show="newBookLoad"
                   :editors="editorBooks"
                   :editorShow="editorBookLoad"
        ></book-show>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import bookShow from 'components/frontComponent/bookShow/bookShow'

  export default {
    data() {
      return {
        list: [],
        mark: 0,
        show: false,
        newBooks: [],
        newBookLoad: false,
        editorBooks: [],
        editorBookLoad: false
      }
    },
    computed: {
      navdown() {
        return this.$store.getters.getNavDown
      },
      category() {
        return this.$store.getters.getCategory
      }
    },
    created() {
      this.$http.get('/front/getCarouselImg').then(res => {
        if (res.status == 200) {
          if (res.data.code === 1) {
            this.$message.warning(res.data.meg)
          } else {
            this.list = res.data.data
          }
        }
      })
      this.$store.dispatch('getCategory')
      this.newBook('玄幻')
      this.editorBook('玄幻')
    },
    methods: {
      changeTright(i) {
        this.mark = i;
        this.newBook(this.category[i].category)
        this.editorBook(this.category[i].category)
      },
      newBook(text) {
        this.newBookLoad = false
        this.$http.get('/front/news?category=' + text).then(res => {
          if (res.data.code === 1) {
            this.$message.warning(res.data.meg)
          } else {
            this.newBooks = res.data.data
            this.newBookLoad = true
          }
        })
      },
      editorBook(text) {
        this.$http.get('/front/editor?category=' + text).then(res => {
          if (res.data.code === 1) {
            this.$message.warning(res.data.meg)
          } else {
            this.editorBooks = res.data.data
            this.editorBookLoad = true
          }
        })
      }
    },
    components: {
      bookShow
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .warp
    .warp_car
      .carouses
        .el-carousel__arrow
          background-color: rgba(216, 54, 54, 0.5);
    .warp_nav
      .nav_down
        width: 100%;
        height: 510px;
        background: #3E3D43;
        .list_li
          display: flex;
          height: 30px;
          line-height: 30px;
          color: #fff;
          padding-top: 6px;
          text-indent: 0.5em;
          cursor: pointer;
          .text
            flex: 1;
          .active
            background: #aa1111;
            color: #fff;
          .mark
            flex: 0 0 20px;
            color: #fff;
          .triangle-right
            margin-top: 10px;
            width: 0;
            height: 0;
            border-top: 5px solid transparent;
            border-left: 10px solid #aa1111;
            border-bottom: 5px solid transparent;
      .fall-enter-active
        transition: all .3s ease;
      .fall-leave-active
        transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
      .fall-enter, .fall-leave-to
        transform: translateX(-100%);
        opacity: 0;
</style>



