<template>
  <div>
    <div class="title">书籍信息修改</div>
    <Cards :list="list"></Cards>
  </div>
</template>

<script>
  import Cards from 'components/serverComponent/card/card'

  export default {
    data() {
      return {
        list: []
      }
    },
    created() {
      this.$http.get('/book/getBookMeg').then(res => {
        if (res.status == 200) {
          if (res.data.code === 1) {
            this.$message.warning(res.data.meg)
          } else {
            this.list = res.data.data
          }
        }
      })
    },
    components: {
      Cards
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
</style>



