<template>
  <transition name="fade">
    <div class="bookcases" v-show="showBook">
      <div class="wrap">
        <div class="icon-叉号" @click="closeBook"></div>
        <div class="titleCase">我的书架</div>
        <div class="book"
             v-if="showBooks"
             v-for="(item,index) in books"
             :key="item._book._id"
             @click="bookRouter(item._book._id)">
          <img :src="item._book.img"/>
          <div class="name">{{item._book.name}}</div>
        </div>
        <div class="book-info" v-if="!showBooks">
          你没有收藏任何书籍喔！
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
  export default {
    data() {
      return {
        books: [],
        showBooks: true
      }
    },
    computed: {
      showBook() {
        return this.$store.getters.getBookcase
      }
    },
    created() {
      this.$http.get('/front/bookcase').then(res => {
        if (res.data.code == 0) {
          if (res.data.data != 0) {
            this.books = res.data.data
          } else {
            this.showBooks = false
            this.books = []
          }
        } else {
          this.showBooks = false
        }
      })
    },
    methods: {
      closeBook() {
        this.$store.dispatch('setBookcase', false)
//          this.openBookcase = false
      },
      bookRouter(id) {
        this.$store.dispatch('setBookcase', false)
        this.$router.push('/getOneBook/' + id)
      }
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  @keyframes hover-up
    0%
      transform: translateY(0)
    100%
      transform: translateY(-30px)
  @keyframes rotate-close
    0%
      transform: rotate(0)
    100%
      transform: rotate(360deg)
  .bookcases
    position: absolute;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.95);
    blur: 10px;
    .wrap
      width: 80%;
      height: 100vh;
      margin: 0 auto;
      overflow: auto;
      display: flex;
      flex-wrap: wrap;
      .icon-叉号
        cursor: pointer;
        z-index: 120;
        position: absolute;
        right: 50px;
        top: 50px;
        font-size: 32px;
        color: #fff;
      .icon-叉号:hover
        animation: rotate-close .5s ease-in-out;
      .titleCase
        margin-top: 50px;
        width: 100%;
        font-size: 24px;
        text-align: center;
        color: #fff;
      .book
        width: 150px;
        height: 220px;
        margin: 50px 0 0 100px;
        cursor: pointer;
        & > img
          width: 150px;
          height: 200px;
        .name
          width: 150px;
          height: 20px;
          text-align: center;
          color: #fff;
      .book:hover
        animation: hover-up .5s ease;
      .book-info
        color: #fff;
  .fade-enter-active, .fade-leave-active
    transition: opacity 1s;
  .fade-enter, .fade-leave-to
    opacity: 0;
</style>



