<template>
  <div>
    <div class="chapterSelect">
      <div class="el-row">
        <div class="el-col el-col-8">
          <div class="grid-content girds" v-if="pres == '-1' || pres == ''">上一章</div>
          <div class="grid-content" v-else>
            <router-link :to="'/getOneChapter/'+pres">上一章</router-link>
          </div>
        </div>
        <div class="el-col el-col-8">
          <div class="grid-content" :class="{'bg_active': itemShow}" @click="selectItems">目录</div>
        </div>
        <div class="el-col el-col-8">
          <div class="grid-content girds" style="border-right: none;" v-if="nexts == '-1' || nexts == ''">下一章</div>
          <div class="grid-content" style="border-right: none;" v-else>
            <router-link :to="'/getOneChapter/'+nexts">下一章</router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        itemShow: false
      }
    },
    props: {
      nexts: {
        type: String,
        default() {
          return ''
        }
      },
      pres: {
        type: String,
        default() {
          return ''
        }
      }
    },
    methods: {
      selectItems() {
        this.itemShow = !this.itemShow
        this.$emit('itemchange', this.itemShow)
      }
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  .chapterSelect
    width: 70%;
    height: 50px;
    margin: 10px auto 10px auto;
    text-align: center;
    background: #F6F1E7;
    .grid-content
      margin: 10px 0;
      height: 30px;
      cursor: pointer;
      border-right: 1px solid #999;
      & > a
        text-decoration: none;
        color: #000;
    .bg_active
      background: #fff;
    .girds
      color: #999;
</style>



