<template>
  <div class="demo-input-suffix">
    <div class="title">编辑推荐添加</div>
    <book-select @option="getItem"></book-select>
    <el-row>
      推荐书籍选择：
      <el-select
        v-model="bookSelect"
        filterable
        remote
        reserve-keyword
        :disabled="opts"
        placeholder="请输入关键词"
        @change="selectBook(options4)"
        :remote-method="remoteMethod"
        :loading="loading">
        <el-option
          v-for="item in options4"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-row>
    <el-row>
      作者：
      <br/><br/>
      <el-input
        :value="actorOp"
        placeholder="请输入作者名"
        :disabled="optactor">
      </el-input>
    </el-row>
    <el-row>
      作品简介：
      <br/><br/>
      <el-input
        :value="briefOp"
        :disabled="optactor"
        type="textarea"
        autosize
        placeholder="请输入内容">
      </el-input>
    </el-row>
    <el-row>
      封面上传：
      <br/><br/>
      <el-upload
        class="upload-demo"
        ref="upload"
        action="/book"
        :on-preview="handlePreview"
        drag
        :on-remove="handleRemove"
        multiple
        :auto-upload="false"
        :limit="1"
        :file-list="fileList"
        :before-upload="beforeUpload"
        :on-exceed="handleExceed">
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
        <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
      </el-upload>
    </el-row>
    <el-row>
      <el-button type="primary" @click="onSubmit">上传服务器</el-button>
      <el-button>取消</el-button>
    </el-row>
  </div>
</template>

<script>
  import bookSelect from 'components/serverComponent/bookselect/bookselect'

  export default {
    data() {
      return {
        options4: [],
        bookSelect: '',
        list: [],
        opts: true,
        optactor: true,
        loading: false,
        actorOp: '',
        briefOp: '',
        idOp: '',
        fileList: [],
        categorySelect: ''
      }
    },
    created() {
      this.$http.get('/book/category').then(res => {
        if (res.status === 200) {
          this.options = res.data.data
        }
      })
    },
    methods: {
      remoteMethod(query) {
        if (query !== '') {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            this.options4 = this.list.filter(item => {
              return item.label.toLowerCase()
                .indexOf(query.toLowerCase()) > -1;
            });
          }, 200);
        } else {
          this.options4 = [];
        }
      },
      selectBook(book) {
//        console.log(book[0].actor)
//        console.log(book)
        this.optactor = false
        this.actorOp = book[0].actor
        this.briefOp = book[0].brief
        this.idOp = book[0].id
      },
      handleRemove(file, fileList) {
        console.log(file, fileList)
      },
      handlePreview(file) {
        console.log(file)
      },
      handleExceed(files, fileList) {
        this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`)
      },
      beforeUpload(file) {
//        console.log(file)
        const fd = new FormData()
        fd.append('category', this.categorySelect)
        fd.append('book', this.bookSelect)
        fd.append('actor', this.actorOp)
        fd.append('brief', this.briefOp)
        fd.append('bookid', this.idOp)
        fd.append('file', file)
        this.$http.post('/book/uploadRecommend', fd).then(res => {
          if (res.status === 200) {
//          console.log(res)
            if (res.data.code === 0) {
              this.$router.push('/editorcard')
            }
            this.$message.warning(res.data.meg)
          }
        })
      },
      onSubmit() {
        if (this.briefOp != '') {
          this.$refs.upload.submit()
        } else {
          this.$message.warning('请完成空白处的内容');
        }
      },
      getItem(opt, items, name) {
        this.opts = opt;
        this.list = items;
        this.categorySelect = name;
      }
    },
    components: {
      bookSelect
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  .demo-input-suffix
    width: 100%;
    margin-left: 10px;
    .el-row
      margin-top: 10px;
</style>



