<template>
  <div id="editor" ref="editor" class="hidden-md-and-down">
    <li class="items" ref="item" :key="index" v-for="(item,index) in list" @click="change(index)"><img
      :src="item.recommendImg"/></li>
    <div class="content">
      <div class="bar">
        <h1>编辑</h1><br/>
        <h1>推荐</h1>
      </div>
      <div class="text">
        <div class="text_title" v-if="list[mark]">
          <h2>{{list[mark].recommendBook}}</h2><br/>
          <h2>{{list[mark].actor}}</h2>
        </div>
        <div class="text_content" v-if="list[mark]">
          <p v-html="list[mark].recommendText"></p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        mark: 4,
        list: [],
        iNow: 4,
        target: -1,
        iTs: true
      }
    },
    created() {
      this.$http.get('/front/EditorImg').then(res => {
        if (res.status == 200) {
          if (res.data.code === 1) {
            this.$message.warning(res.data.meg)
          } else {
            this.list = res.data.data
          }
        }
      })
    },
    methods: {
      change(i) {
        this.mark = i;
//          var translate = getComputedStyle(this.$refs.item[i]).transform;
//          var DidElem = parseFloat(translate.substring(7).split(',')[12]);
        if (!this.iTs) {
          return;
        }
        this.iTs = false;
        this.target = i;
        if (this.target > this.iNow) {
          if (this.target - this.iNow <= 4) {
            this.goNext();
          } else {
            this.goPre();
          }
        } else {
          if (this.iNow - this.target <= 4) {
            this.goPre();
          } else {
            this.goNext();
          }
        }
        // iNow = i;
        // this.$refs.item[i].style.transform = "translate3d(0,0,0) rotateY(0)";
        // this.$refs.item[i].style.zIndex = "55";
      },
      goNext() {
        this.iNow++;
        if (this.iNow > 8) {
          this.iNow = 0;
        }
        this.tab(this.iNow);
        if (this.iNow == this.target) {
          this.iTs = true;
          return;
        }
        setTimeout(() => {
          this.goNext();
        }, 700);
      },
      goPre() {
        this.iNow--;
        if (this.iNow < 0) {
          this.iNow = 8;
        }
        this.tab(this.iNow);
        if (this.iNow == this.target) {
          this.iTs = true;
          return;
        }
        setTimeout(() => {
          this.goPre();
        }, 700);
      },
      tab(n) {
        var a = [8, 6, 4, 2];
        for (var i = 0; i < 4; i++) {
          var Left = n - 1 - i;
          if (Left < 0) {
            Left = Left + 9;
          }
          this.$refs.item[Left].style.transform = "translate3d(" + (-150 * (i + 1)) + "px,0," + (-50 - i * 50) + "px) rotateY(40deg)";
          this.$refs.item[Left].style.zIndex = a[i];
          var Right = n + 1 + i;
          if (Right > 8) {
            Right = Right - 9;
          }
          this.$refs.item[Right].style.transform = "translate3d(" + (150 * (i + 1)) + "px,0," + (-50 - i * 50) + "px) rotateY(-40deg)";
          this.$refs.item[Right].style.zIndex = a[i];
        }
        this.$refs.item[n].style.transform = "translate3d(0,0,0) rotateY(0)";
        this.$refs.item[n].style.zIndex = "10";
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  #editor
    width: 100%;
    height: 600px;
    position: relative;
    transform-style: preserve-3d;
    perspective: 800px;
    .items
      position: absolute;
      left: 45%;
      top: 10%;
      width: 180px;
      height: 240px;
      z-index: 50;
      transition: .5s ease-in-out;
      img
        vertical-align: top;
        border: none;
    .items:nth-of-type(1)
      transform: translate3d(-600px, 0, -200px) rotateY(40deg);
      z-index: 2;
    .items:nth-of-type(2)
      transform: translate3d(-450px, 0, -150px) rotateY(40deg);
      z-index: 5;
    .items:nth-of-type(3)
      transform: translate3d(-300px, 0, -100px) rotateY(40deg);
      z-index: 6;
    .items:nth-of-type(4)
      transform: translate3d(-150px, 0, -50px) rotateY(40deg);
      z-index: 8;
    .items:nth-of-type(5)
      transform: translate3d(0, 0, 0) rotateY(0);
      z-index: 10;
    .items:nth-of-type(6)
      transform: translate3d(150px, 0, -50px) rotateY(-40deg);
      z-index: 8;
    .items:nth-of-type(7)
      transform: translate3d(300px, 0, -100px) rotateY(-40deg);
      z-index: 6;
    .items:nth-of-type(8)
      transform: translate3d(450px, 0, -150px) rotateY(-40deg);
      z-index: 4;
    .items:nth-of-type(9)
      transform: translate3d(600px, 0, -200px) rotateY(-40deg);
      z-index: 2;
    .content
      position: absolute;
      left: 13%;
      top: 55%;
      width: 1500px;
      height: 180px;
      z-index: 1;
      display: flex
      .bar
        width: 15%;
        height: 180px;
        background: #BF2C24;
        float: left;
        h1
          text-align: center;
          font-size: 24px;
          color: #fff;
        h1:nth-of-type(1)
          padding-top: 50px;
      .text
        width: 100%;
        height: 180px;
        float: left;
        color: #fff;
        background-color: rgba(0, 0, 0, 0.5);
        .text_title
          width: 20%;
          height: 180px;
          float: left;
          text-align: center;
          h2:nth-of-type(1)
            font-size: 24px;
            padding-top: 50px;
        .text_content
          width: 80%;
          height: 180px;
          float: left;
          p
            padding-top: 40px;
            line-height: 30px;
            text-align: left;
  @media screen and (max-width: 1600px)
    #editor
      .content
        width: 1200px;
        left: 10%;
</style>



