<template>
    <div>
      <el-aside width="200px">
        <el-menu :default-openeds="['1', '3']" :router="true">
          <el-submenu v-if="isAdmins" index="1">
            <template slot="title"><i class="el-icon-message"></i>用户管理</template>
            <el-menu-item-group>
              <template slot="title">管理员列表</template>
              <el-menu-item index="/userlist">用户列表</el-menu-item>
              <el-menu-item index="/commentmeg">用户评论管理</el-menu-item>
            </el-menu-item-group>
            <!--<el-menu-item-group title="用户列表">-->
              <!--<el-menu-item index="1-3">用户收藏夹</el-menu-item>-->
            <!--</el-menu-item-group>-->
          </el-submenu>
          <el-submenu index="2">
            <template slot="title"><i class="el-icon-menu"></i>书籍管理</template>
            <el-menu-item-group v-if="isAdmins">
              <template slot="title">编辑推荐</template>
              <el-menu-item index="/editoradd">编辑推荐增加</el-menu-item>
              <el-menu-item index="/editorcard">编辑推荐修改</el-menu-item>
            </el-menu-item-group>
            <el-menu-item-group title="书籍信息管理">
              <el-menu-item index="/bookcommit">书籍录入</el-menu-item>
            </el-menu-item-group>
            <el-submenu index="2-4">
              <template slot="title">书籍修改</template>
              <el-menu-item index="/chatermeg">修改与删除</el-menu-item>
            </el-submenu>
            <el-menu-item-group title="章节信息录入">
              <el-menu-item index="/chatsave">章节录入</el-menu-item>
            </el-menu-item-group>
            <el-submenu index="2-4-1">
              <template slot="title">章节修改</template>
              <el-menu-item index="/chaptermeg">修改与删除</el-menu-item>
            </el-submenu>
          </el-submenu>
          <el-submenu index="3" v-if="isAdmins">
            <template slot="title"><i class="el-icon-setting"></i>前台内容添加</template>
            <el-menu-item-group>
              <template slot="title">首页轮播图</template>
              <el-menu-item index="/frontCarouselAdd">添加</el-menu-item>
              <el-menu-item index="/frontCarouselFix">修改</el-menu-item>
            </el-menu-item-group>
            <!--<el-menu-item-group title="分组2">-->
              <!--<el-menu-item index="3-3">选项3</el-menu-item>-->
            <!--</el-menu-item-group>-->
            <!--<el-submenu index="3-4">-->
              <!--<template slot="title">选项4</template>-->
              <!--<el-menu-item index="3-4-1">选项4-1</el-menu-item>-->
            <!--</el-submenu>-->
          </el-submenu>
        </el-menu>
      </el-aside>
    </div>
</template>

<script>
    export default {
      data() {
        return {
          isAdmins: true
        }
      },
      computed: {
        getUser() {
          return this.$store.getters.show
        }
      },
      mounted() {
        if (this.getUser.isAdmin != void 0) {
          this.isAdmins = this.getUser.isAdmin
        } else {
          this.isAdmins = this.getUser[0].isAdmin
        }
      }
    }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  .el-aside
    color: #333;
    li
      a
        text-decoration: none;
        color: #000;
    .is-active
      color: #409EFF !important;
</style>



