<template>
  <div>
    <el-container>
      <el-header>
        <div class="title">章节录入</div>
      </el-header>
      <el-main class="main">
        <book-select @option="getItem"></book-select>
        <el-row>
          书籍选择：
          <el-select
            v-model="bookSelect"
            filterable
            remote
            reserve-keyword
            :disabled="opts"
            placeholder="请输入关键词"
            @change="selectBook(options4)"
            :remote-method="remoteMethod"
            :loading="loading">
            <el-option
              v-for="item in options4"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-row>
        <el-row>
          章节名:
          <el-input
            v-model="title"
            placeholder="请输入标题"
            :disabled="opts"></el-input>
        </el-row>
        <el-row>
          您目前需要更新章节:
          <el-input
            v-model="chapter"
            :disabled="true">
          </el-input>
        </el-row>
        <div id="editor" type="text/plain" style="height:600px"></div>
        <el-row class="chapter_btn">
          <el-button type="primary" @click="submits">保存</el-button>
          <el-button type="warning" @click="out" plain>取消</el-button>
        </el-row>
      </el-main>
    </el-container>
  </div>
</template>

<script>
  import 'server/public/ueditor/ueditor.config'
  import 'server/public/ueditor/ueditor.all.min'
  import 'server/public/ueditor/lang/zh-cn/zh-cn'

  import bookSelect from 'components/serverComponent/bookselect/bookselect'

  export default {
    data() {
      return {
        ue: '',
        uedata: [],
        rudata: [],
        bookSelect: '',
        opts: true,
        options4: '',
        loading: false,
        chapter: 0,
        bookName: '',
        title: '',
        bookid: 0
      }
    },
    created() {
      this.$http.get('/book/category').then(res => {
//          console.log(res)
        if (res.status === 200) {
//          console.log(res)
          this.options = res.data.data
        }
//        console.log(this.userList)
      })
    },
    mounted() {
      this.ue = UE.getEditor('editor', {
        BaseUrl: '',
        UEDITOR_HOME_URL: 'static/ueditor/'
      })
    },
    methods: {
      submits() {
        this.uedata.push(UE.getEditor('editor').getContent());
        var fontLength = UE.getEditor('editor').getContentLength(true)
//        console.log(this.uedata.join("\n"));
//        console.log(this.categorySelect)
//        console.log(this.bookSelect)
        if (this.categorySelect == '' || this.bookSelect == '') {
          alert('请把信息填写完整')
        } else {
          this.$http.post('/book/uploadChapter', {
            name: this.bookSelect,
            category: this.categorySelect,
            title: this.title,
            chapterNum: this.chapter,
            fontNum: fontLength,
            content: this.uedata,
            bookid: this.bookid
          }).then(res => {
//            console.log(res)
            if (res.data.code === 0) {
              UE.getEditor('editor').setContent('')
              this.$router.push('/chaptermeg')
            }
            this.bookSelect = ''
            this.categorySelect = ''
            this.title = ''
            this.chapter = 0
            this.opts = true
            this.$message.warning(res.data.meg)
          })
        }
      },
      xieru() {
        UE.getEditor('editor').setContent('欢迎使用ueditor');
      },
      remoteMethod(query) {
        if (query !== '') {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            this.options4 = this.list.filter(item => {
//              this.bookid = item.id;
//              console.log(item.id)
              return item.label.toLowerCase()
                .indexOf(query.toLowerCase()) > -1;
            });
//            console.log(this.bookid)
          }, 200);
        } else {
          this.options4 = [];
        }
      },
      getItem(opt, items, name) {
        this.opts = opt;
        this.list = items;
        this.categorySelect = name;
      },
      selectBook(book) {
//        this.bookName = book[0].id
        this.bookid = book[0].id
        this.$http.post('/book/Chapter', {
          name: this.bookSelect,
          category: this.categorySelect
        }).then(res => {
          if (res.status === 200) {
//            console.log(res)
            if (res.data.code === 3) {
              this.chapter = res.data.chapterNum
            }
            if (res.data.code === 0) {
              this.chapter = res.data.chapterNum + 1
            }
            if (res.data.code === 1) {
              this.$message.warning(res.data.meg)
            }
          }
        })
      },
      out() {
        this.$router.push('/serverIndex')
      }
    },
    components: {
      bookSelect
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  .el-container
    margin-left; 10px !important;
    .chapter_btn
      margin-top: 50px;
      float: left;
</style>



