import * as types from './mutation_type'
import Vue from 'vue'

export default {
  [types.CHECKUSER](state, bData) {
    // console.log(bData)
    state.user = bData;
    // console.log(state.user)
    Vue.prototype.$http.post('/user/loginUser', state.user).then(res => {
      // console.log(res)
      if (res.status == 200) {
        if (res.data.code === 1) {
          state.loginType = 0;
          alert('用户名或密码错误')
        } else {
          // state.user = res.data.data
          if (localStorage.getItem('user')) {
            state.user = [...JSON.parse(localStorage.getItem('user'))]
            state.loginType = 2;
            state.loginMiss = false;
            alert('您已经登录了，请勿重复登录')
          } else {
            localStorage.setItem('user', JSON.stringify(res.data.data))
            state.user = res.data.data
            // console.log(state.user)
            state.loginType = 1;
            state.loginMiss = false;
          }
        }
      } else {
        state.loginType = 0;
        alert('请求服务器超时')
//                this.$message.warning('请求服务器超时');
      }
    })
  },
  [types.LOGOUT](state) {
    // console.log('logout')
    state.user = '';
    state.loginType = 0;
    state.warning = '';
    localStorage.clear();
  },
  [types.GETUSER](state) {
    if (localStorage.getItem('user')) {
      // console.log(localStorage.getItem('user'))
      state.user = [JSON.parse(localStorage.getItem('user'))]
      // console.log(state.user)
      state.loginType = 2;
    } else {
      state.warning = '请登录'
      state.user = [];
    }
  },
  [types.GETONECHAPTER](state, data) {
    localStorage.setItem('AChapter', JSON.stringify(data))
    state.chapter = data
  },
  [types.CHECKCHAPTER](state) {
    if (state.chapter.name == void 0) {
      if (localStorage.getItem('AChapter') == '') {
        state.chapter = []
      }
      else {
        state.chapter = [JSON.parse(localStorage.getItem('AChapter'))]
      }
    }
  },
  [types.DELECTACHAPTER](state) {
    state.chapter = ''
    localStorage.setItem('AChapter', '')
  },
  [types.SETFIXUSER](state, userDate) {
    localStorage.setItem('fixuser', JSON.stringify(userDate))
    state.fixUser = userDate
  },
  [types.GETFIXUSER](state) {
    if (state.fixUser[0] == void 0) {
      if (localStorage.getItem('fixuser') == '') {
        // console.log('没有数据')
        state.fixUser = {
          pass: '',
          checkPass: '',
          user: '',
          email: '',
          sex: '男',
          oldPass: ''
        }
      }
      else {
        // console.log('获取数据')
        state.fixUser = [JSON.parse(localStorage.getItem('fixuser'))]
        if (state.fixUser[0] != void 0) {
          state.fixUser[0].oldPass = ''
          state.fixUser[0].pass = ''
          state.fixUser[0].checkPass = ''
        }
      }
    }
  },
  [types.DELETEFIXUSER](state) {
    state.fixUser = {
      pass: '',
      checkPass: '',
      user: '',
      email: '',
      sex: '男',
      oldPass: ''
    }
    localStorage.setItem('fixuser', '')
  },
  [types.NAVDOWN](state, bol) {
    state.navDown = bol;
  },
  [types.LOGINMISS](state, bol) {
    state.loginMiss = bol;
  },
  [types.LOGOUTMISS](state, bol) {
    state.logoutMiss = bol;
  },
  [types.GETCATEGORY](state) {
    if (state.category.length == 0) {
      Vue.prototype.$http.get('/front/getCategory').then(res => {
        if (res.status == 200) {
          // console.log('111')
          if (res.data.code === 1) {
            this.$message.warning(res.data.meg)
          } else {
            state.category = res.data.data
          }
        }
      })
    }
  },
  [types.GETPAGEBOOK](state, submit) {
    state.bookLoad = false
    // console.log(submit.category)
    if (submit.day == void 0 || submit.day == null) {
      submit.day = 0
    }
    Vue.prototype.$http.get('/front/getAllBookPage?NowPage='
      + submit.page + '&category=' + submit.category + '&day=' + submit.day).then(res => {
      if (res.status == 200) {
        if (res.data.code === 1) {
          Vue.prototype.$message.warning(res.data.meg)
        } else {
//            this.book = res.data.data
          state.numPage = res.data.data.page * 10
          state.book = res.data.data.bookMeg
          // console.log(state.book)
          state.bookLoad = true
//           this.page = res.data.data.page * 10
//           this.book = res.data.data.bookMeg
        }
      }
    })
  },
  [types.GETBOOKMEG](state, id) {
    Vue.prototype.$http.get('/front/getOneBookMeg?id=' + id).then(res => {
      if (res.status == 200) {
        if (res.data.code === 1) {
          Vue.prototype.$message.warning(res.data.meg)
        } else {
          // console.log(res.data)
          state.people = res.data.num
          state.average = res.data.average
          state.fontNum = res.data.fontNum
          state.point = res.data.point
          state.bookmeg = res.data.data
        }
      }
    })
  },
  [types.CHAPTERUSER_INIT](state) {
    if (localStorage.getItem('chapteruser')) {
      state.chapterUser = JSON.parse(localStorage.getItem('chapteruser'))
    } else {
      localStorage.setItem('chapteruser', JSON.stringify(state.chapterUser))
    }
  },
  [types.CHAPTERUSER_SET](state, fontState) {
    if (fontState.fontState === void 0) {
      state.chapterUser = fontState
    }
    if (fontState.fontState === 'fontColor') {
      state.chapterUser.fontColor = fontState.set
    }
    if (fontState.fontState === 'backgroundColor') {
      state.chapterUser.backgroundColor = fontState.set
    }
    if (fontState.fontState === 'fontSize') {
      state.chapterUser.fontSize = fontState.set
    }
    // console.log(state.chapterUser)
    localStorage.setItem('chapteruser', JSON.stringify(state.chapterUser))
  },
  [types.ALL_RATING](state) {
    state.otherRating.testMockClick = []
    state.otherRating.testMockPoint = []
    state.otherRating.testMockCollect = []
    state.otherRating.testMockCollect = []
    if (state.allRating.testMockClick != '' && state.allRating.testMockCollect != '' &&
      state.allRating.testMockComment != '' && state.allRating.testMockPoint != '') {
      return
    }
    //点击统计
    Vue.prototype.$http.get('/rating/click').then(res => {
      if (res.status == 200) {
        if (res.data.code === 0) {
          state.allRating.testMockClick = res.data.data
        } else {
          this.$message.warning(res.data.meg)
        }
      } else {
        this.$message.warning('网络出错')
      }
    })

    //评分统计
    Vue.prototype.$http.get('/rating/ratingPoint').then(res => {
      if (res.status == 200) {
        if (res.data.code === 0) {
          state.allRating.testMockPoint = res.data.data
        } else {
          this.$message.warning(res.data.meg)
        }
      } else {
        this.$message.warning('网络出错')
      }
    })

    //收藏统计
    Vue.prototype.$http.get('/rating/ratingCollect').then(res => {
      if (res.status == 200) {
        if (res.data.code === 0) {
          state.allRating.testMockCollect = res.data.data
        } else {
          this.$message.warning(res.data.meg)
        }
      } else {
        this.$message.warning('网络出错')
      }
    })

    //热门评论榜
    Vue.prototype.$http.get('/rating/ratingComment').then(res => {
      if (res.status == 200) {
        if (res.data.code === 0) {
          state.allRating.testMockComment = res.data.data
        } else {
          this.$message.warning(res.data.meg)
        }
      } else {
        this.$message.warning('网络出错')
      }
    })
  },
  [types.OTHER_RATING](state, category) {
    //点击统计
    Vue.prototype.$http.get('/rating/click?category=' + category).then(res => {
      if (res.status == 200) {
        if (res.data.code === 0) {
          state.otherRating.testMockClick = res.data.data
        } else {
          this.$message.warning(res.data.meg)
        }
      } else {
        this.$message.warning('网络出错')
      }
    })

    //评分统计
    Vue.prototype.$http.get('/rating/ratingPoint?category=' + category).then(res => {
      if (res.status == 200) {
        if (res.data.code === 0) {
          state.otherRating.testMockPoint = res.data.data
        } else {
          this.$message.warning(res.data.meg)
        }
      } else {
        this.$message.warning('网络出错')
      }
    })

    //收藏统计
    Vue.prototype.$http.get('/rating/ratingCollect?category=' + category).then(res => {
      if (res.status == 200) {
        if (res.data.code === 0) {
          state.otherRating.testMockCollect = res.data.data
        } else {
          this.$message.warning(res.data.meg)
        }
      } else {
        this.$message.warning('网络出错')
      }
    })

    //热门评论榜
    Vue.prototype.$http.get('/rating/ratingComment?category=' + category).then(res => {
      if (res.status == 200) {
        if (res.data.code === 0) {
          state.otherRating.testMockComment = res.data.data
        } else {
          this.$message.warning(res.data.meg)
        }
      } else {
        this.$message.warning('网络出错')
      }
    })
  },
  [types.SEARCH_SET](state, data) {
    if (data[0] == void 0) {
      state.searchDate = []
      state.searchDate.push(data)
    } else {
      state.searchDate = data
    }
  },
  [types.SEARCH_CHECK](state, key) {
    Vue.prototype.$http.get('/front/search?key=' + key).then(res => {
      if (res.data.code === 0) {
        state.searchDate = res.data.data
      } else {
        state.searchDate = []
      }
    })
  },
  [types.BOOKCASE_STATE](state, bol) {
    state.bookcaseState = bol
  }
}



