<template>
  <div class="book-container" v-if="bookMeg">
    <div class="head">
      <div class="img">
        <img :src="bookMeg.img">
      </div>
      <div class="book-meg">
        <div class="titles">{{bookMeg.name}}</div>
        <div class="name">{{bookMeg.actor}}&nbsp;著</div>
        <div class="state">
          <div class="cate">{{bookMeg.category}}</div>
          <div class="over">连载中</div>
        </div>
        <div class="count">
          <div class="meg">字数:</div>
          <div class="font">{{fontNum}}万字</div>
          <div class="meg">点击数：</div>
          <div class="click">{{bookMeg.clickNum / 10000}}万点击</div>
        </div>
        <div class="btn">
          <el-button @click="read(bookMeg._id)" type="primary" class="btns">开始阅读</el-button>
          <el-button type="warning" :plain="plain" @click="likeBook">加入书架</el-button>
        </div>
      </div>
      <div class="rating">
        <div class="point" v-if="people === 0">暂无评价</div>
        <div class="point avr" v-else>{{average}}</div>
        <div class="num">{{people}} 人评价</div>
        <div class="text">我要评价</div>
        <div class="block">
          <el-rate
            @change="rating"
            v-model="value2"
            :colors="['#99A9BF', '#F7BA2A', '#FF9900']">
          </el-rate>
        </div>
      </div>
    </div>
    <bookmeg-select :text="bookMeg.brief" :bookid="bookMeg._id"></bookmeg-select>
  </div>
</template>

<script>
  import bookmegSelect from 'components/frontComponent/bookmegSelect/bookmegSelect'

  export default {
    data() {
      return {
        value2: 0,
        fontNum: 0,
        people: 0,
        average: 0,
        plain: true
      }
    },
    created() {
      this.$store.dispatch('getBookMeg', this.$route.params.id)
      this.$http.get('/book/getColBook?bookid=' + this.$route.params.id).then(res => {
        if (res.status == 200) {
          if (res.data.code === 0) {
            this.plain = false
          }
          if (res.data.code === 1) {
            this.plain = true
          }
        } else {
          this.$message.warning('网络出错')
        }
      })
//      this.$store.dispatch('getComment', bookMeg._id)
//        console.log(this.$route.params.id)
    },
    computed: {
      bookMeg() {
//        console.log(this.$store.getters.getPoint)
        this.people = this.$store.getters.getPeople
        this.average = this.$store.getters.getAverage
        this.fontNum = this.$store.getters.getFontNum
        this.value2 = this.$store.getters.getPoint
        return this.$store.getters.getBookMeg
      }
    },
    methods: {
      rating(val) {
        const {
          category, _id
        } = this.bookMeg
//        console.log(this.$store.getters.getLoginType)
        if (this.$store.getters.getLoginType === 0) {
          this.$store.dispatch('setLoginMiss', true)
        } else {
          this.$http.post('/rating/star', {
            category, _id, rating: val
          }).then(res => {
            if (res.data.code === 1) {
              this.$message.warning(res.data.meg)
            }
            if (res.data.code === 0) {
              if (this.people == 0) {
                this.people = this.people + 1
                this.average = val
              } else {
                this.average = (this.average * this.people + val) / (this.people + 1)
                this.average = Math.round(this.average * 100) / 100
                this.people = this.people + 1
              }
              this.$message.success(res.data.meg)
            }
          })
        }
      },
      read(id) {
//        console.log(id)
        this.$http.get('/front/firstChapter?id=' + id).then(res => {
          if (res.status == 200) {
            if (res.data.code == 0) {
              this.$router.push('/getOneChapter/' + res.data.data._id)
            }
            if (res.data.code == 1) {
              this.$message.warning(res.data.meg)
            }
          }
        })
      },
      likeBook() {
        if (this.plain) {
          if (this.$store.getters.getLoginType === 0) {
            this.$message.warning('请先登录')
            this.$store.dispatch('setLoginMiss', true)
          } else {
            this.$http.get('/book/colletBook?bookid=' + this.$route.params.id).then(res => {
              if (res.status == 200) {
                if (res.data.code === 0) {
                  this.$message.success(res.data.meg)
                  this.plain = false
                }
              } else {
                this.$message.warning('网络出错')
              }
            })
          }
        } else {
          if (this.$store.getters.getLoginType === 0) {
            this.$message.warning('请先登录')
            this.$store.dispatch('setLoginMiss', true)
          } else {
            this.$confirm('此操作将取消收藏, 是否继续?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning',
              center: true
            }).then(() => {
              this.$http.get('/book/delColBook?bookid=' + this.$route.params.id).then(res => {
                if (res.status == 200) {
                  if (res.data.code === 0) {
                    this.$message({
                      type: 'success',
                      message: '取消收藏成功!'
                    });
                  }
                  if (res.data.code === 1) {
                    this.$message.warning(res.data.meg)
                  }
                } else {
                  this.$message.warning('网络出错')
                }
              })
            }).catch(() => {
              this.$message({
                type: 'info',
                message: '已取消收藏'
              });
            });
            this.plain = true
          }
        }
//        this.plain = !this.plain
      }
    },
    components: {
      bookmegSelect
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .book-container
    width: 70%;
    height: auto;
    padding: 10px;
    margin: 0 auto;
    .head
      display: flex;
      width: 100%;
      height: 300px;
      .img
        flex: 0 0 180px;
        width: 180px;
        height: 240px;
        box-shadow: 5px 5px 2px #888;
        overflow: hidden;
        & > img
          width: 100%;
          height: 100%;
          cursor: pointer;
        & > img:hover
          transition: ease .2s;
          transform-origin: 50% 50%;
          transform: scale(1.2);
      .book-meg
        flex: 1;
        .titles
          float: left;
          margin-left: 30px;
          font-size: 24px;
          font-weight: bold;
        .name
          float: left;
          margin-left: 10px;
          line-height: 40px;
        .state
          width: 100%;
          height: 35px;
          clear: both;
          .cate, .over
            margin: 5px 0px 0 25px;
            float: left;
            width: 50px;
            height: 20px;
            padding: 3px;
            border-radius: 10px;
            text-align: center;
            border: 1px solid #bf2c24;
            color: #bf2c24;
        .btn
          width: 100%;
          height: 180px;
          .btns
            margin-left: 20px;
          .el-button--primary
            background-color: #bf2c24;
            border-color: #bf2c24;
        .count
          padding: 10px 30px;
          width: 100%;
          height: 30px;
          display: flex;
          .meg
            color: #999;
          .font
            margin-right: 20px;
      .rating
        flex: 0 0 170px;
        width: 170px;
        & > div
          margin-top: 10px;
        .block
          width: 170px;
        .avr
          color: #aa1111;
          font-size: 24px;
</style>



