<template>
  <div class="send">
    <div class="title">
      <span>有什么想吐槽的事告诉大家？</span>
    </div>
    <el-input
      type="textarea"
      :rows="2"
      :autosize="{ minRows: 2, maxRows: 4}"
      placeholder="请输入内容"
      v-model="textarea"
      @input="text">
    </el-input>
    <div class="ten">
      <div class="emoji" @click="showEmoji">
        <span>😀</span><span>表情</span>
      </div>
      <el-collapse-transition>
        <div class="blocks" v-show="show">
          <li v-for="(item,index) in emoji" :key="index" @click="selectEmoji(index)">{{item}}</li>
        </div>
      </el-collapse-transition>
    </div>
    <div class="btn">
      <el-button type="primary" @click="sendMeg" :disabled="sends">发送</el-button>
    </div>
    <div class="meg">
      <transition-group name="downs">
        <div v-if="item.content != ''" class="user" v-for="(item,index) in meg" :key="item._id">
          <div class="state">
            <div class="icon-用户"></div>
            <div class="name">{{item._user.user}}</div>
            <div class="time">{{item.createTime}}</div>
          </div>
          <div class="content">
            <pre>{{item.content}}</pre>
          </div>
        </div>
        <div class="user down" :key="123" v-else>
          暂无评论
        </div>
      </transition-group>
    </div>
    <div class="block" v-if="this.meg[0].content != ''">
      <el-pagination
        @current-change="handleCurrentChange"
        layout="prev, pager, next"
        :total="page">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        textarea: '',
        sends: true,
        auto: false,
        emoji: '',
        show: false,
        meg: [
          {
            _user: {
              user: ''
            },
            content: '',
            _id: '123',
            time: new Date().toLocaleString()
          }
        ],
        sendMegs: [{
          _user: {
            user: ''
          },
          content: '',
          _id: '123'
        }],
        page: 10
      }
    },
    computed: {
      getUser() {
//        console.log(this.$store.getters.show)
        if (this.$store.getters.show.length == 0) {
          return false
        } else {
          if (this.$store.getters.show[0] == void 0) {
            return this.$store.getters.show.user
          } else {
            return this.$store.getters.show[0].user
          }
        }
      }
    },
    mounted() {
      const emoji = '😀 😃 😄 😁 😆 😅 😂 😊 😇 🙂 🙃 😉 😌 😍 😘 😗 😙 😚 😋 😜 😝 😛 🤑 🤗 🤓 😎 😏 😒 😞 😔 😟 😕 🙁 😣 😖 😫 😩 😤 😠 😡 😶 😐 😑 😯 😦 😧 😮 😲 😵 😳  '
      this.emoji = emoji.split(' ')
      if (this.textarea == '') {
        this.sends = true
      }
      this.$http.get('/comment/getMeg?bookid=' + this.$route.params.id).then(res => {
        if (res.status == 200) {
          if (res.data.code === 0) {
            if (res.data.data == 0) {
              this.meg = [
                {
                  _user: {
                    user: ''
                  },
                  content: ''
                }
              ]
            } else {
//              this.meg = res.data.data.CommentMeg
              this.meg = res.data.data.CommentMeg.map(res => {
                var oldtime = Date.parse(res.createTime)
                var newDate = new Date();
                newDate.setTime(oldtime)
                res.createTime = newDate.toLocaleString()
                return res
              })
              this.page = res.data.data.page * 10
            }
          }
        } else {
          this.$message.warning('网络出错')
        }
      })
    },
    methods: {
      showEmoji() {
        this.show = !this.show
      },
      text() {
        var cont = this.trim(this.textarea, 1)
        if (cont != '') {
          this.sends = false
        } else {
          this.sends = true
        }
      },
      trim(str, type) {
        switch (type) {
          case 1:
            return str.replace(/\s+/g, "");
          case 2:
            return str.replace(/(^\s*)|(\s*$)/g, "")
          case 3:
            return str.replace(/(^\s*)/g, "")
          case 4:
            return str.replace(/(\s*$)/g, "")
          default:
            return str
        }
      },
      selectEmoji(i) {
        this.textarea = this.textarea + this.emoji[i]
        this.sends = false
        this.show = false
      },
      sendMeg() {
        if (this.$store.getters.getLoginType === 0) {
          this.$message.warning('请先登录')
          this.$store.dispatch('setLoginMiss', true)
        } else {
          this.$http.post('/comment/com', {
            content: this.textarea,
            bookid: this.$route.params.id
          }).then(res => {
            if (res.status == 200) {
              if (res.data.code === 0) {
                this.$message.warning(res.data.meg)
              }
              if (res.data.code === 1) {
                this.$message.warning(res.data.meg)
              }
            } else {
              this.$message.warning('网络出错')
            }
          })
          var sendMegs = [{
            _user: {
              user: ''
            },
            content: '',
            _id: '123',
            createTime: new Date().toLocaleString()
          }]
          sendMegs[0]._id = Math.random()
          sendMegs[0].content = this.textarea
          sendMegs[0]._user.user = this.getUser
//          sendMegs[0].time = new Date().toLocaleString()
//          console.log(this.sendMegs[0]._user.username)
          if (this.meg[0].content == '') {
            this.meg = sendMegs
          } else {
            this.meg = sendMegs.concat(this.meg)
            if (this.meg.length > 3) {
              this.meg.pop()
            }
          }
//          console.log(this.meg)
//          this.sendMegs = this.sendMegs[0]
          this.textarea = ''
        }
//        console.log(this.sendMegs)
      },
      handleCurrentChange(val) {
        this.$http.get('/comment/getMeg?bookid=' + this.$route.params.id + '&NowPage=' + val).then(res => {
          if (res.status == 200) {
            if (res.data.data == 0) {
              this.meg = [
                {
                  _user: {
                    user: ''
                  },
                  content: ''
                }
              ]
            } else {
              this.meg = res.data.data.CommentMeg
              this.page = res.data.data.page * 10
            }
          } else {
            this.$message.warning('网络出错')
          }
        })
      }
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
    @keyframes down
      0%
        opacity: 0;
        transform: translateY(-200px)
      100%
        opacity: 1;
        transform: translateY(0)
    .send
      position: relative;
      width: 80%;
      height: auto;
      margin: 0 auto;
      .title
        color: #aa1111;
        font-size: 14px;
        margin-bottom: 10px;
      .el-textarea
        & > textarea
          resize: none;
      .ten
        position: relative;
        top: 0;
        left: 0;
        height: 200px;
        .emoji
          cursor: pointer;
        .blocks
          position: absolute;
          top: 20px;
          left: -10px;
          width: 300px;
          z-index: 5;
          height: 200px;
          overflow: auto;
          & > li
            width: 20px;
            height: 20px;
            padding: 5px;
            float: left;
            cursor: pointer;
      .btn
        position: absolute;
        top: 110px;
        left: 0;
        width: 100%;
        height: 50px;
        margin-top: 20px;
        z-index: 1;
        & > button
          float: right;
        .el-button--primary
          background-color: #bf2c24;
          border-color: #bf2c24;
        .is-disabled
          background-color: #aa4849;
          border-color: #aa4849;
      .downs-enter-active
        animation: down .5s;
      .downs-leave-active
        animation: down .5s reverse;
      .meg
        width: 100%;
        height: auto;
        border-top: 1px solid #ddd;
        .down
          animation: down .5s ease;
        .user
          width: 100%;
          height: 200px;
          border-bottom: 1px solid #ddd;
          .state
            width: 100%;
            height: 45px;
            .icon-用户
              margin: 10px 0 0 10px;
              font-size: 32px;
              float: left;
            .name
              font-weight: bold;
              margin-left: 10px;
              line-height: 45px;
              float: left;
            .time
              color: #999;
              font-size: 14px;
              line-height: 45px;
              float: right;
          .content
            width: 80%;
            height: 150px;
            margin: 0 auto;
            overflow: hidden;
      .block
        width: 300px;
        height: 30px;
        margin: 0 auto;
        margin-bottom: 20px;
</style>



