<template>
  <div>
    <el-container>
      <aside-nav></aside-nav>
      <router-view></router-view>
      <!--<Mains></Mains>-->
    </el-container>
  </div>
</template>

<script>
  import Headers from 'components/serverComponent/header/header'
  import AsideNav from 'components/serverComponent/asidenav/asidenav'
  import cookies from 'browser-cookies'

  export default {
    name: 'serverIndex',
    data() {
      return {
        user: []
      }
    },
    components: {
      Headers,
      AsideNav
    },
    created() {
      if (cookies.get('userid') == null || cookies.get('userid') == void 0) {
        this.$router.push('/')
        this.$message.warning('请登录，否则无法进入后台')
      }
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  .title
    text-align: center;
    font-size: 24px;
    margin: 10px 0;
</style>



