<template>
  <div>
    <div class="title">编辑推荐修改</div>
    <Cards :list="list"></Cards>
  </div>
</template>

<script>
  import Cards from 'components/serverComponent/card/card'

  export default {
    data() {
      return {
        currentDate: new Date(),
        list: []
      }
    },
    created() {
      this.$http.get('/book/getRecommend').then(res => {
        if (res.status == 200) {
          this.list = res.data.data
        }
      })
    },
    components: {
      Cards
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  .el-row
    .el-col
      .el-card
        img
          margin: 0 auto;
          display: block;
        .title
          width: 100%;
          display: inline-block;
          text-align: center;
        .text
          height: 160px;
        .btn
          width: 180px;
          margin: 10px auto;
</style>



