<template>
  <div class="bookShow" v-show="navdown">
    <el-row :gutter="30" class="wrappers">
      <div class="titles">推荐书籍</div>
      <el-col
        :lg="6" :md="6" :sm="6" :xs="12"
        v-for="item in editors" :class="{'hidden-xs-only':item == 2 || item == 3}"
        :key="item._id">
        <div class="grid-content book">
          <router-link :to="'/getOneBook/'+item.bookid">
            <div class="imgbox rotate" v-show="editorShow">
              <img :src="item.recommendImg">
            </div>
            <div class="titles rotate" v-show="editorShow">{{item.recommendBook}}</div>
          </router-link>
        </div>
      </el-col>
    </el-row>
    <el-row :gutter="30" class="wrappers">
      <div class="titles">新书推荐</div>
      <el-col
        :lg="6" :md="6" :sm="6" :xs="12"
        v-for="item in news"
        :class="{'hidden-xs-only':item == 2 || item == 3}"
        :key="item._id">
        <div class="grid-content book">
          <router-link :to="'/getOneBook/'+item._id">
            <div class="imgbox rotate" v-show="show">
              <img :src="item.img">
            </div>
            <div class="titles rotate" v-show="show">{{item.name}}</div>
          </router-link>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    props: {
      news: {
        type: Array,
        default() {
          return []
        }
      },
      show: {
        type: Boolean,
        default() {
          return false
        }
      },
      editors: {
        type: Array,
        default() {
          return []
        }
      },
      editorShow: {
        type: Boolean,
        default() {
          return false
        }
      }
    },
    computed: {
      navdown() {
        return this.$store.getters.getNavDown
      }
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
    @keyframes rotateYS
      0%
        transform: rotateY(360deg)
      100%
        transform: rotateY(0)
    .bookShow
      width: 100%;
      height: 510px;
      background: #3E3D43;
      .wrappers
        width: 90%;
        height: auto;
        margin: 0 auto !important;
        .titles
          padding-top: 5px;
          color: #fff;
      .book
        cursor: pointer;
        margin: 20px auto;
        height: 180px;
        .imgbox
          width: 50%;
          height: 0;
          position: relative;
          padding-top: 150px;
          margin: 0 auto;
          & > img
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        .titles
          padding-top: 5px;
          width: 100%;
          text-align: center;
        .rotate
          animation: rotateYS .5s ease;
    @media screen and (max-width: 1200px)
      .bookShow
        .book
          .imgbox
            width: 80%;
</style>



