<template>
  <div>
    <div class="title_left">分类</div>
    <div class="all_item" :class="{'active':mark===-1}" @click="all()">全部</div>
    <div class="item">
      <div
        class="text"
        v-for="(item,index) in category"
        :class="{'active':mark===index}"
        @click="change(index)"
        :key="index">{{item.category}}
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        mark: -1
      }
    },
    props: {
      category: {
        type: Array,
        default() {
          return []
        }
      }
    },
    methods: {
      change(i) {
        this.mark = i;
        this.$emit('change', i)
      },
      all() {
        this.mark = -1
        this.$emit('all', -1)
      }
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  .title_left
    width: 100%;
    text-align: left;
    font-size: 16px;
    font-weight: bold;
    margin: 10px 0;
  .active
    color: #fff;
    background: #BF2C24;
  .all_item
    width: 100%;
    height: 30px;
    border-radius: 10px;
    line-height: 30px;
    margin-bottom: 10px;
    text-align: center;
    cursor: pointer;
  .item
    width: 100%;
    height: 120px;
    border-bottom: 1px solid #ddd;
    .text
      width: 60px;
      height: 20px;
      text-align: center;
      line-height: 20px;
      border-radius: 10px;
      float: left;
      cursor: pointer;
    .active
      color: #fff;
      background: #BF2C24;
</style>



