<template>
  <transition name="fade">
    <div class="register" v-show="logOutState">
      <div class="wrap">
        <div class="icon-叉号" @click="outLogout()"></div>
        <div class="title">注册</div>
        <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px"
                 class="demo-ruleForm">
          <el-form-item label="用户名" prop="user">
            <el-input type="text" v-model="ruleForm2.user" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="pass">
            <el-input type="password" v-model="ruleForm2.pass" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="checkPass">
            <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="性别">
            <el-radio v-model="ruleForm2.sex" label="男">男</el-radio>
            <el-radio v-model="ruleForm2.sex" label="女">女</el-radio>
          </el-form-item>
          <el-form-item
            prop="email"
            label="邮箱"
            :rules="[
      {  message: '请输入邮箱地址', trigger: 'blur' },
      { type: 'email', message: '请输入正确的邮箱地址', trigger: 'blur,change' }
    ]"
          >
            <el-input v-model="ruleForm2.email"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>
            <el-button @click="resetForm('ruleForm2')">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </transition>
</template>

<script>
  export default {
    data() {
      var checkUser = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('用户名不能为空'))
        }
        setTimeout(() => {
          if (value.length < 4) {
            return callback(new Error('用户名必须超过4位'))
          } else {
            return callback()
          }
        }, 1000);
      }
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.ruleForm2.checkPass !== '') {
            this.$refs.ruleForm2.validateField('checkPass');
          }
          callback();
        }
      }
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.ruleForm2.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      }
      return {
        ruleForm2: {
          pass: '',
          checkPass: '',
          user: '',
          email: '',
          sex: '男'
        },
        rules2: {
          pass: [
            {
              validator: validatePass, trigger: 'blur'
            }
          ],
          checkPass: [
            {
              validator: validatePass2, trigger: 'blur'
            }
          ],
          user: [
            {
              validator: checkUser, trigger: 'blur'
            }
          ]
        }
      };
    },
    computed: {
      logOutState() {
        return this.$store.getters.getLogoutState
      }
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
//            console.log(this.ruleForm2)
            this.$http.post('/user/sendUser', this.ruleForm2).then(res => {
              if (res.status == 200) {
                if (res.data.code === 1) {
                  this.$message.warning(res.data.meg);
                  this.$refs[formName].resetFields();
                } else {
                  this.$message.warning(res.data.meg);
                  this.$refs[formName].resetFields();
                }
              } else {
                this.$message.warning('请求服务器超时');
              }
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      outLogout() {
        this.$store.dispatch('setLogoutMiss', false)
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
    @keyframes rotate-close
      0%
        transform: rotate(0)
      100%
        transform: rotate(360deg)
    .register
      position: absolute;
      top: 0;
      left: 0;
      z-index: 100;
      width: 100%;
      height: 100vh;
      background-color: rgba(7, 17, 27, 0.8);
      blur: 10px;
      .icon-叉号
        cursor: pointer;
        z-index: 120;
        position: absolute;
        right: 50px;
        top: 50px;
        font-size: 32px;
        color: #fff;
      .icon-叉号:hover
        animation: rotate-close .5s ease-in-out;
      .title
        font-size: 32px;
        color: #fff;
        text-align: center;
        margin-bottom: 30px;
        padding-top: 30px;
      .wrap
        width: 80%;
        height: auto;
        margin: 0 auto;
        label
          color: #fff;
        button
          margin-right: 10px;
          float: right;
    .fade-enter-active, .fade-leave-active
      transition: opacity 1s;
    .fade-enter, .fade-leave-to
      opacity: 0;
</style>



