<template>
  <div class="tab">
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="作品信息" name="first">
        <div class="text" v-html="text">
        </div>
      </el-tab-pane>
      <el-tab-pane label="目录（章节）" name="second">
        <el-row v-if="chapterMeg !== ''">
          <el-col class="e-cols" :span="6" v-for="(items,index) in chapterMeg" :key="items._id">
            <div class="chapter">
              <router-link :to="'/getOneChapter/'+items._id">
                {{index + 1}}. {{items.title}}
              </router-link>
            </div>
          </el-col>
        </el-row>
        <el-row v-else>
          <el-col :span="6">
            <div class="chapter">{{noChater}}</div>
          </el-col>
        </el-row>
      </el-tab-pane>
      <el-tab-pane label="作品讨论（条）" name="third">
        <send-meg></send-meg>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
  import sendMeg from 'components/frontComponent/sendMeg/sendMeg'

  export default {
    data() {
      return {
        activeName: 'first',
        chapterMeg: [],
        noChater: '暂时没有该书籍的章节'
      };
    },
    props: {
      text: {
        type: String,
        default() {
          return '暂无信息'
        }
      }
    },
    methods: {
      handleClick(tab, event) {
        if (this.activeName === 'second') {
//          console.log(this.bookid)
          this.$http.get('/front/getChapter?bookid=' + this.$route.params.id).then(res => {
            if (res.status === 200) {
              if (res.data.code === 0) {
                this.chapterMeg = res.data.data
              }
              if (res.data.code === 2) {
                this.noChater = res.data.data
              }
              if (res.data.code === 1) {
                this.$message.warning(res.data.meg)
              }
            } else {
              this.$message.warning('网络有错误')
            }
          })
        }
      }
    },
    components: {
      sendMeg
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  .tab
    width: 100%;
    height: auto;
    .el-tab-pane
      .e-cols
        height: 80px;
        .chapter
          padding: 0 0;
          margin-top: 10px;
          & > a
            color: #000;
            text-decoration: none;
          & > a:hover
            color: #aa1111;
    .el-tabs__item.is-active
      color: #aa1111 !important;
    .el-tabs__active-bar
      background: #aa1111 !important;
    .el-tabs__item:hover
      color: #aa1111 !important;
</style>



