<template>
  <div class="forms">
    <div class="title">章节内容修改</div>
    <el-main v-if="getChapter" class="eMain">
      <el-form ref="dataChapter" :model="dataChapter" label-width="100px">
        <el-form-item label="书籍类别">
          <el-input v-model="dataChapter.category"></el-input>
        </el-form-item>
        <el-form-item label="书籍名">
          <el-input v-model="dataChapter.name"></el-input>
        </el-form-item>
        <el-form-item label="标题">
          <el-input v-model="dataChapter.title"></el-input>
        </el-form-item>
        <el-form-item label="章节">
          <el-input v-model="dataChapter.chapterNum"></el-input>
        </el-form-item>
        <div id="editor" type="text/plain" style="height:600px"></div>
        <el-form-item class="btns">
          <el-button type="primary" @click="onSubmit">保存修改</el-button>
          <el-button>取消</el-button>
        </el-form-item>
      </el-form>
    </el-main>
    <el-main class="eMain" v-else>
      <el-form ref="dataChapter" :model="dataChapter" label-width="100px">
        <el-form-item label="书籍类别">
          <el-input v-model="dataChapter.category"></el-input>
        </el-form-item>
        <el-form-item label="书籍名">
          <el-input v-model="dataChapter.name"></el-input>
        </el-form-item>
        <el-form-item label="标题">
          <el-input v-model="dataChapter.title"></el-input>
        </el-form-item>
        <el-form-item label="章节">
          <el-input v-model="dataChapter.chapterNum"></el-input>
        </el-form-item>
        <div id="editor_fix" type="text/plain" style="height:600px"></div>
        <el-form-item class="btns">
          <el-button type="primary" @click="onSubmit">保存修改</el-button>
          <el-button @click="chapterOut">取消</el-button>
        </el-form-item>
      </el-form>
    </el-main>
  </div>
</template>

<script>
  import 'server/public/ueditor/ueditor.config'
  import 'server/public/ueditor/ueditor.all.min'
  import 'server/public/ueditor/lang/zh-cn/zh-cn'

  export default {
    data() {
      return {
        dataChapter: []
      }
    },
    mounted() {
      this.ue = UE.getEditor('editor_fix', {
        BaseUrl: '',
        UEDITOR_HOME_URL: 'static/ueditor/',
      })
      this.ue.ready(() => {
        this.ue.setContent(this.dataChapter.content)
      })
    },
    computed: {
      getChapter() {
        if (this.$store.state.chapter.name != void 0) {
          this.dataChapter = this.$store.state.chapter
          return this.$store.state.chapter
        } else {
          this.$store.dispatch('checkOneChapter')
          setTimeout(() => {
            if (this.$store.state.chapter[0] != void 0 && this.$store.state.chapter[0].name != void 0) {
              this.dataChapter = this.$store.state.chapter[0]
              return this.$store.state.chapter[0]
            } else {
              this.$message.warning('请录入好数据，再来操作')
              this.$router.push('/chaptermeg')
              return false
            }
          })
        }
      }
    },
    methods: {
      onSubmit() {
      },
      chapterOut() {
        this.$store.dispatch('delectChapter')
        this.$router.push('/chaptermeg')
      }
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  .eMain
    width: 100%;
    .btns
      margin-top: 10px;
</style>



