import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import index from 'page/index/index'
import register from 'page/register/register'
import login from 'page/login/login'
import bookmeg from 'page/bookmeg/bookmeg'
import all from 'page/allBook/allbook'
import rating from 'page/rating/rating'
import getOneChapter from 'page/getOneChapter/getOneChapter'
import searchPage from 'page/searchPage/searchPage'
import chat from 'page/chat/chat'

import serverIndex from 'page/serverPage/index/index'
import enter from 'page/serverPage/index/enter'
import userlist from 'page/serverPage/index/userlist'
import editoradd from 'page/serverPage/index/editoradd'
import editorcard from 'page/serverPage/index/editorcard'
import bookcommit from 'page/serverPage/index/bookcommit'
import chatermeg from 'page/serverPage/index/chatermeg'
import frontCarouselAdd from 'page/serverPage/index/frontCarouselAdd'
import frontCarouselFix from 'page/serverPage/index/frontCarouselFix'
import chatsave from 'page/serverPage/index/chatersave'
import chaptermeg from 'page/serverPage/index/chaptermeg'
import commentmeg from 'page/serverPage/index/commentmeg'

import fixbrief from 'page/serverPage/fix/fixbrief'
import fixchapter from 'page/serverPage/fix/fixchapter'
import fixuser from 'page/serverPage/fix/fixuser'

// import Mains from 'components/serverComponent/maincontaniner/maincontaniner'
// import headers from 'components/serverComponent/header/header'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/serverIndex',
      name: 'serverIndex',
      component: serverIndex,
      children: [
        {
          path: '/', component: enter
        },
        {
          path: '/userlist', component: userlist
        },
        {
          path: '/editoradd', component: editoradd
        },
        {
          path: '/editorcard', component: editorcard
        },
        {
          path: '/bookcommit', component: bookcommit
        },
        {
          path: '/chatermeg', component: chatermeg
        },
        {
          path: '/chatsave', component: chatsave
        },
        {
          path: '/frontCarouselAdd', component: frontCarouselAdd
        },
        {
          path: '/frontCarouselFix', component: frontCarouselFix
        },
        {
          path: '/fixBrief',
          name: 'fixBrief',
          component: fixbrief
        },
        {
          path: '/fixchapter',
          name: 'fixchapter',
          component: fixchapter
        },
        {
          path: '/chaptermeg', component: chaptermeg
        },
        {
          path: '/fixuser', component: fixuser
        },
        {
          path: '/commentmeg', component: commentmeg
        }
      ]
    },
    {
      path: '/register',
      name: 'register',
      component: register
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/all',
      name: 'all',
      component: all
    },
    {
      path: '/getOneBook/:id',
      name: 'BookMeg',
      component: bookmeg
    },
    {
      path: '/getOneChapter/:id',
      name: 'getOneChapter',
      component: getOneChapter
    },
    {
      path: '/rating',
      name: 'rating',
      component: rating
    },
    {
      path: '/search',
      name: 'search',
      component: searchPage
    },
    {
      path: '/chat',
      name: 'chat',
      component: chat
    }
  ]
})



