import * as types from './mutation_type'

export default {
  login({commit}, bData) {
    commit(types.CHECKUSER, bData);
  },
  logout({commit}) {
    commit(types.LOGOUT);
  },
  getInit({commit}) {
    commit(types.GETUSER);
  },
  getOneChapter({commit}, data) {
    commit(types.GETONECHAPTER, data)
  },
  checkOneChapter({commit}) {
    commit(types.CHECKCHAPTER);
  },
  delectChapter({commit}) {
    commit(types.DELECTACHAPTER);
  },
  setFixUser({commit}, userDate) {
    commit(types.SETFIXUSER, userDate);
  },
  getFixUser({commit}) {
    commit(types.GETFIXUSER);
  },
  deleteFixUser({commit}) {
    commit(types.DELETEFIXUSER)
  },
  setNavDown({commit}, bol) {
    commit(types.NAVDOWN, bol)
  },
  setLoginMiss({commit}, bol) {
    commit(types.LOGINMISS, bol)
  },
  setLogoutMiss({commit}, bol) {
    commit(types.LOGOUTMISS, bol)
  },
  getCategory({commit}) {
    commit(types.GETCATEGORY)
  },
  getPageBook({commit}, submit) {
    commit(types.GETPAGEBOOK, submit)
  },
  getBookMeg({commit}, id) {
    commit(types.GETBOOKMEG, id)
  },
  chapterUser_Init({commit}) {
    commit(types.CHAPTERUSER_INIT)
  },
  chapterUser_Set({commit}, fontState) {
    commit(types.CHAPTERUSER_SET, fontState)
  },
  allRating({commit}) {
    commit(types.ALL_RATING)
  },
  otherRating({commit}, category) {
    commit(types.OTHER_RATING, category)
  },
  setSearch({commit}, data) {
    commit(types.SEARCH_SET, data)
  },
  checkSearch({commit}, key) {
    commit(types.SEARCH_CHECK, key)
  },
  setBookcase({commit}, bol) {
    commit(types.BOOKCASE_STATE, bol)
  }
}



