<template>
  <div class="block">
    <el-pagination
      @current-change="handleCurrentChange"
      layout="prev, pager, next"
      :total="page">
    </el-pagination>
  </div>
</template>

<script>
  export default {
    props: {
      categorys: {
        type: Array,
        default() {
          return []
        }
      },
      nums: {
        type: Number,
        default() {
          return -2
        }
      },
      time: {
        type: Number,
        default() {
          return 0
        }
      }
    },
    watch: {
      nums(val) {
        this.getBook()
      }
    },
    data() {
      return {
        nowPage: 1,
        book: []
      }
    },
    computed: {
      page() {
        return this.$store.getters.getPage
      }
    },
    created() {
      this.getBook()
//      this.handleCurrentChange(this.nowPage)
    },
    methods: {
      handleCurrentChange(val) {
        this.nowPage = val
        this.getBook()
      },
      getBook() {
        var selects = {}
        if (this.nums < 0) {
          selects.page = this.nowPage
          selects.day = this.time
          selects.category = 'all'
          this.$store.dispatch('getPageBook', selects)
        } else {
          selects.page = this.nowPage
          selects.day = this.time
          selects.category = this.categorys[this.nums].category
          this.$store.dispatch('getPageBook', selects)
        }
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .block
    width: 300px;
    height: 30px;
    margin: 0 auto;
    margin-bottom: 20px;
</style>



