<template>
  <div>
    <el-main>
      用户列表
      <el-table
        :data="userList"
        stripe
        v-loading="loading2"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
        style="width: 100%">
        <el-table-column
          prop="create_time"
          label="创建时间"
          sortable
          width="200">
        </el-table-column>
        <el-table-column
          prop="user"
          label="用户名"
        >
        </el-table-column>
        <el-table-column
          prop="sex"
          label="性别">
        </el-table-column>
        <el-table-column
          prop="mail"
          label="邮箱"
          width="180">
        </el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="50">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">修改</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        userList: [],
        loading2: true
      }
    },
    methods: {
      handleClick(i) {
        if (i) {
          this.$store.dispatch('setFixUser', i)
          this.$router.push('/fixuser')
        }
      }
    },
    created() {
      this.$http.get('/user').then(res => {
//          console.log(res)
        if (res.status === 200) {
//          console.log(res)
          this.userList = res.data.data.map(res => {
            var oldtime = res.create_time
            var newDate = new Date();
            newDate.setTime(oldtime)
            res.create_time = newDate.toLocaleString()
            return res
          })
          this.loading2 = false
        }
//        console.log(this.userList)
      })
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
</style>



