<template>
  <div>
    <el-row>
      书籍类别选择：
      <el-select
        v-model="categorySelect"
        @change="selectItem"
        placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.num"
          :label="item.category"
          :value="item.category">
        </el-option>
      </el-select>
    </el-row>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        options: [],
        categorySelect: '',
        list: [],
        states: []
      }
    },
    created() {
      this.$http.get('/book/category').then(res => {
        if (res.status === 200) {
          this.options = res.data.data
        }
      })
    },
    methods: {
      selectItem(item) {
        var bookname = item;
        this.opts = false;
        this.$http.post('/book/getCategoryBook', {
          catgory: bookname
        }).then(res => {
          if (res.status === 200) {
//          console.log(res)
            this.states = res.data.data
            this.list = this.states.map(item => {
              return {
                value: item.name, label: item.name, actor: item.actor, brief: item.brief, id: item._id
              };
            });
//            console.log(bookname)
            this.$emit('option', this.opts, this.list, bookname)
          }
        })
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
</style>



