<template>
  <div>
    <el-row>
      <el-col :lg="6" :md="8" v-for="(item, index) in list" :key="index">
        <el-card :body-style="{ padding: '0px' }">
          <img :src="item.recommendImg||item.img" class="image">
          <div class="card_main" style="padding: 14px;">
            <span class="title">{{item.recommendBook || item.name}}</span>
            <div class="sub_title" style="text-align: center">{{item.category}}</div>
            <div class="text" style="height: 250px; overflow: hidden" v-html="item.recommendText || item.brief"></div>
            <div class="btn">
              <div class="btns">
                <el-button type="primary">
                  <router-link
                    :to="{path:'/fixBrief',query: {id: item._id,hasRecommend:item.recommendBook?true:false}}">修改
                  </router-link>
                </el-button>
              </div>
              <div class="btns">
                <el-button type="warning" @click="out(item._id)">删除</el-button>
              </div>
              <div class="btns" v-if="item.up != void 0">
                <el-button type="info" @click="upEditor(item._id)" v-if="item.up==0">置顶</el-button>
                <el-button type="success" @click="downEditor(item._id)" v-else>置顶</el-button>
              </div>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        show: true
      }
    },
    props: {
      list: {
        type: Array,
        default() {
          return []
        }
      }
    },
    methods: {
      out(id) {
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          if (this.list[0].recommendBook != void 0) {
            /* 推荐书籍删除 */
            this.$http.post('/book/deleteRecommend', {
              id
            }).then(res => {
              if (res.status == 200) {
                if (res.data.code === 0) {
                  this.$message({
                    type: 'success',
                    message: '删除成功!'
                  });
                  window.location.reload();
                } else {
                  this.$message({
                    type: 'warning',
                    message: '删除失败!'
                  });
                }
              }
            })
          } else {
            /* 书籍删除 */
            this.$http.post('/book/deleteBook', {
              id
            }).then(res => {
              if (res.status == 200) {
                if (res.data.code === 0) {
                  this.$message({
                    type: 'success',
                    message: '删除成功!'
                  });
                  window.location.reload();
                } else {
                  this.$message({
                    type: 'warning',
                    message: '删除失败!'
                  });
                }
              }
            })
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      upEditor(id) {
        var ups = 1
        this.$http.post('/book/fixup', {
          id, up: ups
        }).then(res => {
          if (res.status == 200) {
            if (res.data.code === 0) {
              this.$router.go(0)
            }
            this.$message.warning(res.data.meg)
          }
        })
      },
      downEditor(id) {
        var ups = 0
        this.$confirm('此操作将取消置顶, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }).then(() => {
          this.$message({
            type: 'success',
            message: '取消成功!'
          })
          this.$http.post('/book/fixup', {
            id, up: ups
          }).then(res => {
            if (res.status == 200) {
              if (res.data.code === 0) {
                this.$message.warning('置顶取消成功')
                this.$router.go(0)
              }
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已经退出操作'
          })
        })
      }
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  .card_main
    .btn
      width: 100% px;
      height: 50px;
      display: flex;
      .btns
        margin-right: 10px;
        & > button
          float: left;
          a
            text-decoration: none;
            color: #fff;
</style>



