<template>
  <div class="all">
    <div class="left_bar">
      <category-select :category="category" @change="change" @all="all"></category-select>
      <div class="title_left">状态</div>
      <div class="state" @click="changeState()">
        <div class="text" :class="{'active': state===0}">连载</div>
        <div class="text" :class="{'active': state===1}">完本</div>
      </div>
      <!--<div class="title">字数</div>-->
      <!--<div class="font" @click="changeFont()">-->
      <!--<div class="text" data-font="0" :class="{'active': fontState===0}">30万字余下</div>-->
      <!--<div class="text" data-font="1" :class="{'active': fontState===1}">30-50万字</div>-->
      <!--<div class="text" data-font="2" :class="{'active': fontState===2}">50-100万字</div>-->
      <!--<div class="text" data-font="3" :class="{'active': fontState===3}">100-200万字</div>-->
      <!--<div class="text" data-font="4" :class="{'active': fontState===4}">200万字以上</div>-->
      <!--</div>-->
      <div class="title_left">更新时间</div>
      <div class="time" @click="changeTime()">
        <div class="text" data-font="3" :class="{'active': timeState===3}">三日内</div>
        <div class="text" data-font="7" :class="{'active': timeState===7}">七日内</div>
        <div class="text" data-font="15" :class="{'active': timeState===15}">半月内</div>
        <div class="text" data-font="30" :class="{'active': timeState===30}">一月内</div>
      </div>
    </div>
    <div class="container">
      <book-item></book-item>
      <page-select :categorys="category" :nums="mark" :time="timeState"></page-select>
    </div>
  </div>
</template>

<script>
  import bookItem from 'components/frontComponent/bookItem/bookItem'
  import pageSelect from 'components/frontComponent/pageSelect/pageSelect'
  import categorySelect from 'components/frontComponent/categorySelect/categorySelect'

  export default {
    data() {
      return {
        mark: -1,
        state: -1,
        fontState: -1,
        timeState: 0
      }
    },
    computed: {
      category() {
        return this.$store.getters.getCategory
      }
    },
    created() {
      this.$store.dispatch('getCategory')
    },
    methods: {
      change(i) {
        this.mark = i;
      },
      all() {
        this.mark = -1;
      },
      changeState() {
        if (event.target.innerHTML === '连载') {
          this.state = 0;
        }
        if (event.target.innerHTML === '完本') {
          this.state = 1;
        }
      },
      changeFont() {
//        console.log(+event.target.getAttribute('data-font'))
        if (event.target.getAttribute('data-font')) {
          this.fontState = +event.target.getAttribute('data-font');
        }
      },
      changeTime() {
        var selectTime = event.target.getAttribute('data-font')
        if (this.timeState == event.target.getAttribute('data-font')) {
          this.timeState = -1
          this.checkBook()
          return
        }
        if (event.target.getAttribute('data-font')) {
          this.timeState = +selectTime;
        }
        this.checkBook()
      },
      checkBook() {
        var selects = {}
        if (this.timeState < 0) {
          this.timeState = 0
        }
        if (this.mark < 0) {
          selects.page = 1
          selects.category = 'all'
          selects.day = this.timeState
          this.$store.dispatch('getPageBook', selects)
        } else {
          selects.page = 1
          selects.category = this.category[this.mark].category
          selects.day = this.timeState
          this.$store.dispatch('getPageBook', selects)
        }
      }
    },
    components: {
      bookItem,
      pageSelect,
      categorySelect
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  .all
    width: 90%;
    height: auto;
    margin: 0 auto;
    display: flex;
    .left_bar
      width: 220px;
      height: auto;
      flex: 0 0 220px;
      .title_left
        width: 100%;
        text-align: left;
        font-size: 16px;
        font-weight: bold;
        margin: 10px 0;
      .state
        width: 100%;
        height: 30px;
        border-bottom: 1px solid #ddd;
        .text
          width: 60px;
          height: 20px;
          text-align: center;
          line-height: 20px;
          border-radius: 10px;
          float: left;
          cursor: pointer;
        .active
          color: #fff;
          background: #BF2C24;
      .font
        width: 100%;
        height: 80px;
        border-bottom: 1px solid #ddd;
        .text
          width: 100px;
          height: 20px;
          text-align: center;
          line-height: 20px;
          border-radius: 10px;
          cursor: pointer;
          float: left;
          .active
            color: #fff;
            background: #BF2C24;
      .time
        width: 100%;
        height: 30px;
        border-bottom: 1px solid #ddd;
        .text
          width: 55px;
          height: 20px;
          text-align: center;
          line-height: 20px;
          border-radius: 10px;
          cursor: pointer;
          float: left;
          .active
            color: #fff;
            background: #BF2C24;
    .container
      width: 100%;
      height: auto;
      flex: 1;
</style>



