<template>
  <div class="forms">
    <el-main>
      <el-form ref="formData" :model="formData" label-width="100px">
        <el-form-item label="推荐书籍类别">
          <el-input v-model="formData.category"></el-input>
        </el-form-item>
        <el-form-item label="推荐书籍名">
          <el-input v-model="formData.recommendBook||formData.name"></el-input>
        </el-form-item>
        <el-form-item label="推荐书籍作者">
          <el-input v-model="formData.actor"></el-input>
        </el-form-item>
        <el-form-item label="推荐简介">
          <el-input type="textarea" :rows="5" v-model="formData.recommendText||formData.brief"></el-input>
        </el-form-item>
        <el-form-item label="原始图片">
          <img :src="formData.recommendImg||formData.img"/>
        </el-form-item>
        <el-form-item label="封面修改">
          <el-upload
            class="upload-demo"
            ref="upload"
            action="javascript:;"
            :on-preview="handlePreview"
            drag
            :on-remove="handleRemove"
            multiple
            :auto-upload="false"
            :limit="1"
            :file-list="fileList"
            :before-upload="beforeUpload"
            :on-exceed="handleExceed">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
            <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
          </el-upload>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">保存修改</el-button>
          <el-button @click="outRe">取消</el-button>
        </el-form-item>
      </el-form>
    </el-main>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        formData: {
          category: '',
          recommendBook: '',
          actor: '',
          recommendText: ''
        },
        fileList: []
      }
    },
    mounted() {
      if (this.$route.query.hasRecommend) {
        this.$http.post('/book/getRecommendBookId', {
          id: this.$route.query.id
        }).then(res => {
          if (res.status == 200) {
            if (res.data.code === 0) {
              this.formData = res.data.data
            }
          }
        })
      } else {
        this.$http.post('/book/getBookId', {
          id: this.$route.query.id
        }).then(res => {
          if (res.status == 200) {
            if (res.data.code === 0) {
              this.formData = res.data.data
            }
          }
        })
      }
    },
    methods: {
      onSubmit() {
        if (this.$route.query.hasRecommend) {
          if (!this.$refs.upload.submit()) {
            this.$http.post('/book/RecommendNoImgUpdate', {
              id: this.$route.query.id,
              category: this.formData.category,
              recommendBook: this.formData.recommendBook,
              actor: this.formData.actor,
              recommendText: this.formData.recommendText
            }).then(res => {
              if (res.status == 200) {
                if (res.data.code === 0) {
                  this.$router.push('/editorcard')
                }
                this.$message.warning(res.data.meg)
              }
            })
          }
        } else {
          if (!this.$refs.upload.submit()) {
            this.$http.post('/book/BookNoImgUpdate', {
              id: this.$route.query.id,
              category: this.formData.category,
              name: this.formData.name,
              actor: this.formData.actor,
              brief: this.formData.brief
            }).then(res => {
              if (res.status == 200) {
                if (res.data.code === 0) {
                  this.$router.push('/chatermeg')
                }
                this.$message.warning(res.data.meg)
              }
            })
          }
        }
      },
      outRe() {
        if (this.$route.query.hasRecommend) {
          this.$router.push('/editorcard')
        } else {
          this.$router.push('/chatermeg')
        }
      },
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      handleExceed(files, fileList) {
        this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
      },
      beforeUpload(file) {
        const fd = new FormData()
        if (this.$route.query.hasRecommend) {
          fd.append('id', this.$route.query.id)
          fd.append('category', this.formData.category)
          fd.append('recommendBook', this.formData.recommendBook)
          fd.append('actor', this.formData.actor)
          fd.append('recommendText', this.formData.recommendText)
          fd.append('oldimg', this.formData.recommendImg)
          fd.append('file', file)
          this.$http.post('/book/updateImgRecommend', fd).then(res => {
            if (res.status === 200) {
//          console.log(res)
              this.$message.warning(res.data.meg);
            }
          })
        } else {
          fd.append('id', this.$route.query.id)
          fd.append('category', this.formData.category)
          fd.append('name', this.formData.name)
          fd.append('actor', this.formData.actor)
          fd.append('brief', this.formData.brief)
          fd.append('oldimg', this.formData.img)
          fd.append('file', file)
          this.$http.post('/book/updateImgBook', fd).then(res => {
            if (res.status === 200) {
              this.$message.warning(res.data.meg);
            }
          })
        }
      }
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  .forms
    width: 80%;
</style>



