<template>
  <div class="demo-input-suffix">
    <div class="title">书籍初始录入</div>
    <el-row>
      <label>书籍类别选择：</label>
      <el-select
        v-model="categorySelect"
        @change="selectItem"
        placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.num"
          :label="item.category"
          :value="item.category">
        </el-option>
      </el-select>
    </el-row>
    <el-row>
      <label style="line-height: 40px">书名输入:</label>
      <el-input
        style="width: 80%;"
        v-model="bookname"
        :disabled="opts"
        placeholder="请输入书名"
      ></el-input>
    </el-row>
    <el-row>
      <label style="line-height: 40px">作者名:</label>
      <el-input
        style="width: 80%;"
        v-model="actor"
        :disabled="opts"
        placeholder="请输入作者大名"
      ></el-input>
    </el-row>
    <el-row>
      <label style="line-height: 75px">书籍简介:</label>
      <el-input
        type="textarea"
        :rows="3"
        style="width: 80%;"
        v-model="brief"
        :disabled="opts"
        placeholder="请输入内容"
      ></el-input>
    </el-row>
    <el-row>
      封面上传：
      <br/><br/>
      <el-upload
        class="upload-demo"
        ref="upload"
        action="/book"
        drag
        multiple
        :auto-upload="false"
        :limit="1"
        :file-list="fileList"
        :before-upload="beforeUpload">
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
        <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
      </el-upload>
    </el-row>
    <el-row>
      <el-button type="primary" @click="onSubmit">上传服务器</el-button>
      <el-button>取消</el-button>
    </el-row>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        options: [],
        categorySelect: '',
        opts: true,
        bookname: '',
        fileList: [],
        actor: '',
        brief: ''
      }
    },
    created() {
      this.$http.get('/book/category').then(res => {
//          console.log(res)
        if (res.status === 200) {
//          console.log(res)
          this.options = res.data.data
        }
//        console.log(this.userList)
      })
    },
    methods: {
      selectItem(item) {
        this.opts = false
      },
      beforeUpload(file) {
//        console.log(file)
        const fd = new FormData()
        fd.append('category', this.categorySelect)
        fd.append('book', this.bookname)
        fd.append('actor', this.actor)
        fd.append('brief', this.brief)
        fd.append('file', file)
        this.$http.post('/book/uploadBook', fd).then(res => {
          if (res.status === 200) {
//          console.log(res)
            if (res.data.code === 0) {
              this.$router.push('/chatermeg')
            }
            this.$message.warning(res.data.meg);
            this.brief = ''
            this.actor = ''
            this.bookname = ''
          }
        })
      },
      onSubmit() {
//        console.log(this.bookname)
        if (this.brief != '' && this.actor != '' && this.categorySelect != '' && this.bookname != '') {
          this.$refs.upload.submit()
        } else {
          this.$message.warning('请完成空白处的内容');
        }
      }
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  .demo-input-suffix
    width: 100%;
    margin-left: 10px;
    .el-row
      margin-top: 10px;
</style>



