<template>
  <div>
      <el-row>
        <el-col :span="8">
          <div class="grid-content bg-purple">Logo</div>
        </el-col>
        <el-col :span="8">
          <div class="grid-content bg-purple-light"></div>
        </el-col>
        <el-col :span="8">
          <div class="grid-content bg-purple">
            <el-button type="text" class="btn" @click="logout">退出</el-button>
            <div class="text">{{user}}</div>
          </div>
        </el-col>
      </el-row>
  </div>
</template>

<script>
  import browserCookie from 'browser-cookies'

  export default {
    props: {
      user: ''
    },
    methods: {
      logout() {
        this.$confirm('此操作将退出登陆, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }).then(() => {
          this.$message({
            type: 'success',
            message: '退出成功!'
          });
          this.$store.dispatch('logout')
          browserCookie.erase('userid')
          this.$router.push('/')
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消退出'
          });
        });
      }
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  .grid-content
    border-radius: 4px;
    min-height: 36px;
    .text
      line-height: 36px;
      margin-right: 10px;
      float: right;
    .btn
      margin-right: 10px;
      cursor: pointer;
      float: right;
</style>



