<template>
  <div id="app">
    <Headers></Headers>
    <Navs></Navs>
    <router-view></router-view>
    <Login></Login>
    <register></register>
    <bookcase></bookcase>
  </div>
</template>

<script>
  import Headers from 'components/frontComponent/header/header'
  import Navs from 'components/frontComponent/nav/nav'
  import bookcase from 'components/frontComponent/bookcase/bookcase'
  import Login from 'page/login/login'
  import register from 'page/register/register'
  import cookies from 'browser-cookies'

  export default {
    name: 'app',
    components: {
      Headers,
      Navs,
      Login,
      register,
      bookcase
    },
    created() {
      if (cookies.get('userid') == null || cookies.get('userid') == void 0) {
        this.$store.dispatch('logout')
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  *
    margin: 0;
    padding: 0;
    list-style: none;
    font-family: '微软雅黑';
  #app
    width: 100%;
    font-family: '微软雅黑';
</style>



