<template>
  <div class="form">
    <div class="title">首页轮播图添加</div>
    <el-form lable-width="100px" class="demo-ruleForm">
      <el-form-item label="标题:">
        <el-input type="text" v-model="title" auto-complete="off" class="inputs"></el-input>
      </el-form-item>
      <el-form-item label="图片上传:">
        <el-upload
          class="upload-demo"
          ref="upload"
          action="/book"
          drag
          multiple
          :auto-upload="false"
          :limit="1"
          :file-list="fileList"
          :before-upload="beforeUpload">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
          <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">上传服务器</el-button>
        <el-button type="info">取消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        title: '',
        fileList: []
      }
    },
    methods: {
      beforeUpload(file) {
        const fd = new FormData()
        fd.append('title', this.title)
        fd.append('file', file)
        this.$http.post('/front/saveCarouselImg', fd).then(res => {
          if (res.status === 200) {
//          console.log(res)
            if (res.data.code === 0) {
              this.$router.push('/frontCarouselFix')
            }
            this.$message.warning(res.data.meg);
            this.title = ''
          }
        })
      },
      onSubmit() {
        if (!this.title) {
          this.$message.warning('标题输入不能为空')
        } else {
          this.$refs.upload.submit()
        }
      }
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  .form
    width: 100%;
    .demo-ruleForm
      width: 80%;
      margin: 0 auto;
      margin-top: 20px;
      .inputs
        width: 60%;
</style>



