<template>
  <div class="serch-page">
    <el-col :span="24">
      <div v-if="searchBook" v-for="(item,index) in searchBook" :key="item._id" class="book">
        <div class="left_img">
          <img :src="item.img" width="150" height="200">
        </div>
        <div class="meg">
          <div class="title">
            <router-link :to="'/getOneBook/'+item._id">
              {{item.name}}
            </router-link>
          </div>
          <div class="state">
            <div class="name"> {{item.actor}}</div>
            <div class="bar">|</div>
            <div class="category">{{item.category}}</div>
            <div class="bar">|</div>
            <div class="book_state">连载中</div>
          </div>
          <router-link :to="'/getOneBook/'+item._id">
            <div class="resume" v-html="item.brief"></div>
          </router-link>
        </div>
      </div>
      <div class="book" v-if="!searchBook">
        暂无查到该书
      </div>
    </el-col>
  </div>
</template>

<script>
  export default {
    computed: {
      searchBook() {
        if (this.$store.getters.getSearch != '') {
          return this.$store.getters.getSearch
        } else {
          return false
        }
      }
    },
    created() {
      this.$store.dispatch('checkSearch', this.$route.query.key)
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .serch-page
    width: 80%
    height: auto;
    margin: 0 auto;
    .book
      display: flex;
      width: 100%;
      height: 240px;
      .left_img
        flex: 0 0 180px;
        overflow: hidden;
        & > img
          padding: 10px;
        & > img:hover
          transition: ease .2s;
          transform-origin: 50% 50%;
          transform: scale(1.2);
      .meg
        flex: 1;
        .title
          text-align: center;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          margin-top: 10px;
          line-height: 30px;
          height: 30px;
          font-size: 24px;
          & > a
            text-decoration: none;
            color: #000;
          & > a:hover
            text-decoration: underline;
        .state
          line-height: 25px;
          height: 25px;
          overflow: hidden;
          .name, .bar, .category, .book_state
            float: left;
          .name, .category, .book_state
            width: 32%;
            text-align: center;
        & > a
          text-decoration: none;
          color: #000;
        & > a:hover
          text-decoration: none;
          color: #ccc;
        .resume
          width: 95%;
          height: 150px;
          margin-top: 5px;
          overflow: hidden;
          text-overflow: ellipsis;
</style>



