<template>
  <div>
    <el-main>
      评论管理列表
      <el-table
        :data="commentList"
        stripe
        v-loading="loading2"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
        style="width: 100%">
        <el-table-column
          prop="createTime"
          label="评论时间"
          sortable
          width="200">
        </el-table-column>
        <el-table-column
          prop="_user.user"
          sortable
          label="用户名"
        >
        </el-table-column>
        <el-table-column
          prop="_book.category"
          sortable
          label="分类">
        </el-table-column>
        <el-table-column
          prop="_book.name"
          sortable
          label="评论书籍"
        >
        </el-table-column>
        <el-table-column
          prop="content"
          label="评论内容"
          width="200"
        >
        </el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="50">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        commentList: [],
        loading2: true
      }
    },
    methods: {
      handleClick(row) {
//          console.log(row)
        this.$confirm('删除该评论, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }).then(() => {
          this.$http.post('/comment/deleteComment', {
            id: row._id
          }).then(res => {
            if (res.status == 200) {
              if (res.data.code == 0) {
                this.$message.success(res.data.meg)
                this.$router.go(0)
              } else {
                this.$message.warning(res.data.meg)
              }
            } else {
              this.$message.warning('网络出错')
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      }
    },
    created() {
      this.$http.get('/comment/getCommentMeg').then(res => {
//          console.log(res)
        if (res.status === 200) {
//          console.log(res)
          this.commentList = res.data.data.map(res => {
            var oldtime = Date.parse(res.createTime)
            var newDate = new Date();
            newDate.setTime(oldtime)
            res.createTime = newDate.toLocaleString()
            return res
          })
          this.loading2 = false
        }
//        console.log(this.userList)
      })
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
</style>



