<template>
  <div class="ratingMeg">
    <div class="left_bar">
      <category-select :category="category" @change="change" @all="all"></category-select>
    </div>
    <div class="container">
      <div class="title margins">排行榜</div>
      <div class="list">
        <div class="column-rating">
          <ratingList :testMock="allrating.testMockClick" :title="click" :banners="bannerCilck"></ratingList>
          <ratingList :testMock="allrating.testMockPoint" :title="point" :banners="bannerPoint"></ratingList>
        </div>
        <div class="column-rating">
          <ratingList :testMock="allrating.testMockCollect" :title="collect" :banners="bannerCollect"></ratingList>
          <ratingList :testMock="allrating.testMockComment" :title="comment" :banners="bannerCollect"></ratingList>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import categorySelect from 'components/frontComponent/categorySelect/categorySelect'
  import ratingList from 'components/frontComponent/ratingList/list'

  export default {
    data() {
      return {
        mark: -1,
        testMockClick: [],
        testMockPoint: [],
        testMockCollect: [],
        testMockComment: [],
        click: '热门点击榜',
        point: '热门评分榜',
        collect: '热门收藏榜',
        comment: '热门评论榜',
        bannerCilck: '点击',
        bannerPoint: '分',
        bannerCollect: '人',
        categorySelect: ''
      }
    },
    created() {
      this.$store.dispatch('getCategory')
      this.$store.dispatch('allRating')
    },
    computed: {
      category() {
        return this.$store.getters.getCategory
      },
      allrating() {
        if (this.$store.getters.otherRating.testMockClick != '') {
          return this.$store.getters.otherRating
        }
        return this.$store.getters.getallRating
      }
    },
    methods: {
      change(i) {
        this.mark = i;
        this.categorySelect = this.category[this.mark].category
        this.$store.dispatch('otherRating', this.categorySelect)
//          console.log(this.category[this.mark].category)
      },
      all() {
        this.mark = -1;
        this.$store.dispatch('allRating')
      }
    },
    components: {
      categorySelect,
      ratingList
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  .ratingMeg
    width: 90%;
    height: auto;
    margin: 0 auto;
    display: flex;
    .left_bar
      width: 220px;
      height: auto;
      flex: 0 0 220px;
    .container
      width: 100%;
      height: auto;
      flex: 1;
      .margins
        margin: 10px 0 0 20px;
      .list
        margin-top: 40px;
        width: 100%;
        height: auto;
        display: flex;
        flex-wrap: wrap;
        align-content: space-between;
        .column-rating
          flex-basis: 50%;
          display: flex;
          justify-content: space-between;
  @media screen and (max-width: 1500px)
    .ratingMeg
      .container
        .list
          .column-rating
            flex-basis: 100%;
</style>



