<template>
  <div>
    <div id="header">
      <div class="logo">
        <img src="static/img/logo.png" width="187" height="72">
      </div>
      <div class="searchs">
        <el-autocomplete
          class="inline-input"
          v-model="search"
          :fetch-suggestions="querySearch"
          placeholder="请输入内容"
          :trigger-on-focus="false"
          @select="handleSelect"
        ></el-autocomplete>
        <el-button type="primary" @click="searchs">搜索</el-button>
      </div>
      <div class="user" v-show="loginState">
        <p v-if="getUser">Hello, {{getUser}}</p>
        <router-link to="/serverIndex">欢迎进入后台管理</router-link>
        <el-button type="text" class="btn" @click="logouts">退出</el-button>
      </div>
      <div class="user" v-show="!loginState">
        <div class="icon-用户" @click="details"></div>
        <div class="text">
          <transition name="detail1">
            <a class="inner inner1" @click="login" v-show="detail">登陆</a>
          </transition>
          <transition name="detail2">
            <a class="inner inner2" @click="reg" v-show="detail">注册</a>
          </transition>
        </div>
      </div>
      <div class="bookcase" @click="myBook">我的书架</div>
    </div>
  </div>
</template>

<script>
  import browserCookie from 'browser-cookies'

  export default {
    name: 'header',
    data() {
      return {
        iClose: false,
        detail: false,
        logout: true,
        logins: false,
        userInfo: '',
        username: '',
        searchData: [],
        search: '',
        cbResult: [],
        selectData: []
      }
    },
    created: function () {
      this.$store.dispatch('getInit')
    },
    computed: {
      loginState() {
        return this.$store.getters.getLoginType
      },
      getUser() {
        if (this.$store.getters.show.length == 0) {
          return false
        } else {
          if (this.$store.getters.show[0] == void 0) {
            return this.$store.getters.show.user
          } else {
            return this.$store.getters.show[0].user
          }
        }
      }
    },
    methods: {
      login() {
        this.$store.dispatch('setLoginMiss', true)
      },
      reg() {
        this.$store.dispatch('setLogoutMiss', true)
      },
      details() {
        this.detail = !this.detail;
      },
      logouts() {
        this.$confirm('此操作将退出登陆, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }).then(() => {
          this.$message({
            type: 'success',
            message: '退出成功!'
          });
          this.$store.dispatch('logout')
          browserCookie.erase('userid')
          this.$router.push('/')
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消退出'
          });
        });
      },
      querySearch(queryString, cb) {
        // 调用 callback 返回建议列表的数据
        this.$http.get('/front/search?key=' + queryString).then(res => {
          if (res.data.code === 0) {
            this.searchData = res.data.data
            this.cbResult = this.searchData.map(res => {
              res.value = res.name
              res.data = res
              return res
            })
            cb(this.cbResult)
          }
        })
      },
      handleSelect(val) {
        this.selectData = val
      },
      searchs() {
        if (this.selectData != '') {
          this.$store.dispatch('setSearch', this.selectData)
          this.$router.push({
            path: '/search', query: {
              key: this.search
            }
          })
//          this.$router.push('/search')
          this.search = ''
        } else {
          this.$store.dispatch('setSearch', this.cbResult)
          this.$router.push({
            path: '/search', query: {
              key: this.search
            }
          })
          this.search = ''
        }
      },
      myBook() {
        if (this.$store.getters.getLoginType === 0) {
          this.$message.warning('请先登录')
          this.$store.dispatch('setLoginMiss', true)
        } else {
          this.$store.dispatch('setBookcase', true)
        }
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
    @keyframes opc
      0%
        opacity: 0;
      100%
        opacity: 1;
    #header
      position: relative;
      width: 100%;
      height: 90px;
      margin: 0 auto;
      margin-bottom: 10px;
      .logo
        width: 240px;
        float: left;
        text-align: center;
        & > img
          padding-top: 10px;
          padding-left: 20px;
      .searchs
        width: 380px;
        height: 40px;
        position: absolute;
        top: 35px;
        left: 40%;
        .el-input__inner
          width: 300px;
          height: 30px;
        .el-button--primary
          width: 50px;
          height: 30px;
          text-align: center;
          background-color: #bf2c24;
          border-color: #bf2c24;
          padding: 0 0;
      .bookcase
        width: 100px;
        height: 40px;
        margin: 32px 40px 0 0;
        text-align: center;
        line-height: 40px;
        cursor: pointer;
        border: 1px solid;
        float: right;
      .bookcase:hover
        color: #fff;
        background-color: #bf2c24;
        animation: opc .2s ease;
      .user
        width: 140px;
        height: 88px;
        float: right;
        padding-top: 10px;
        h1
          margin: 10px 0;
        .icon-用户
          font-size: 30px;
          text-align: center;
          line-height: 88px;
          float: left;
          cursor: pointer;
        .btn
          float: right;
          margin-right: 10px;
        .text
          float: left;
          margin-left: 5px;
          line-height: 88px;
          .detail1-enter-active
            transform: translateX(-40px);
            opacity: 0;
            animation: detail1-in .5s;
          .detail1-leave-active
            animation: detail1-in .5s .5s reverse;
          @keyframes detail1-in
            0%
              transform: translateX(-40px);
              opacity: 0;
            100%
              transform: translateX(0);
              opacity: 1;
          .detail2-enter-active
            transform: translateX(-40px);
            opacity: 0;
            animation: detail2-in .5s .5s;
          .detail2-leave-active
            animation: detail2-in .5s reverse;
          @keyframes detail2-in
            0%
              transform: translateX(-40px);
              opacity: 0;
            100%
              transform: translateX(0);
              opacity: 1;
          a
            display: inline-block;
            width: 30px;
            height: 30px;
            line-height: 30px;
            text-align: center;
            cursor: pointer;
            font-size: 12px;
            border: 1px solid;
            border-radius: 50%;
 @media screen and (max-width: 1200px)
   #header
     .searchs
       width: 265px;
       height: 40px;
       position: absolute;
       top: 35px;
       left: 40%;
       .el-input__inner
         width: 200px;
         height: 30px;
</style>



