<template>
  <div>
    <div class="title">首页轮播图修改</div>
    <el-row>
      <el-col :span="12" v-for="(item, index) in list" :key="index">
        <el-card :body-style="{ padding: '0px' }">
          <img :src="item.img" class="image">
          <div style="padding: 14px;">
            <div style="margin: 10px auto; ">标题: {{item.title}}</div>
            <el-button type="primary">
              修改
            </el-button>
            <el-button type="warning" @click="out(item._id)">删除</el-button>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        list: []
      }
    },
    created() {
      this.$http.get('/front/getCarouselImg').then(res => {
        if (res.status == 200) {
          if (res.data.code === 1) {
            this.$message.warning(res.data.meg)
          } else {
            this.list = res.data.data
          }
        }
      })
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
</style>



