<template>
  <div id="list">
    <div class="title">{{title}}</div>
    <div class="ban">
      <div class="left"></div>
      <div class="right"></div>
    </div>
    <el-collapse v-model="activeNames" @change="handleChange" accordion>
      <el-collapse-item
        v-for="(item,index) in testMock"
        v-if="index < 10"
        :name="index" :key="item._id">
        <template slot="title">
          <div class="num" v-if="index<3">NO.{{index + 1}}</div>
          <div class="grey" v-else>{{index + 1}}</div>
          <div class="text">{{item.name || item._book.name}}</div>
        </template>
        <div class="li_left">
          <router-link v-if="item._book != void 0" :to="'/getOneBook/'+item._book._id">
            <div class="pick">{{item.averPoint || item.num}}{{banners}}</div>
            <div class="name">{{item._book.category}} / {{item._book.actor}}</div>
          </router-link>
          <router-link v-else :to="'/getOneBook/'+item._id">
            <div class="pick">{{item.clickNum}}{{banners}}</div>
            <div class="name">{{item.category}} / {{item.actor}}</div>
          </router-link>
        </div>
        <div class="li_right">
          <router-link v-if="item._book != void 0" :to="'/getOneBook/'+item._book._id">
            <img :src="item._book.img">
          </router-link>
          <router-link v-else :to="'/getOneBook/'+item._id">
            <img :src="item.img">
          </router-link>
        </div>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
  export default {
    name: 'list',
    data() {
      return {
        activeNames: 0
      }
    },
    props: {
      title: {
        type: String,
        default() {
          return '原创风云榜'
        }
      },
      testMock: {
        type: Array,
        default() {
          return []
        }
      },
      banners: {
        type: String,
        default() {
          return '点击'
        }
      }
    },
    methods: {
      handleChange(val) {
//        console.log(val);
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  #list
    width: 280px;
    height: 850px;
    margin: 0 auto;
    .title
      font-size: 16px;
      text-align: left;
      margin: 0 0 10px 20px;
    .ban
      width: 280px;
      height: 6px;
      .left
        width: 140px;
        height: 6px;
        float: left;
        background: #BF2C24;
      .right
        width: 140px;
        height: 6px;
        float: left;
        background: #ccc;
    .el-collapse
      .el-collapse-item__header
        .num
          width: 70px;
          height: 20px;
          font-size: 16px;
          border: 1px solid #BF2C24;
          border-radius: 10px;
          line-height: 20px;
          text-align: center;
          color: #fff;
          background: #BF2C24;
          float: left;
          margin-top: 12px;
          margin-right: 10px;
        .grey
          width: 30px;
          height: 30px;
          line-height: 30px;
          float: left;
          margin-top: 8px;
          margin-right: 10px;
          background: #ddd;
          text-align: center;
          border-radius: 50%;
        .text
          margin: 10px 0;
      .el-collapse-item__content
        .li_left
          width: 140px;
          height: 120px;
          margin: 10px 10px;
          float: left;
          & > a
            text-decoration: none;
            color: #000;
          .pick
            font-size: 24px;
            color: #BF2C24;
            margin-bottom: 10px;
        .li_right
          float: left;
          width: 80px;
          height: 100px;
          margin: 15px 0 0 10px;
          & > a
            & > img
              width: 100%;
              height: 100%;
</style>



