import Vue from 'vue';
import Vuex from 'vuex'
import actions from './action'
import mutations from './mutation'

Vue.use(Vuex)

//loginType  0 - 表示未登录  1 - 表示登录成功  2 - 表示已经登录
const store = new Vuex.Store({
  state: {
    user: [],
    loginType: 0,
    warning: '',
    chapter: [],
    fixUser: {},
    navDown: false,
    loginMiss: false,
    logoutMiss: false,
    category: [],
    book: [],
    numPage: 10,
    bookmeg: [],
    bookLoad: false,
    point: 0,
    fontNum: 0,
    people: 0,
    average: 0,
    chapterUser: {
      fontColor: '#000',
      backgroundColor: '#F6F1E7',
      fontSize: '16px'
    },
    allRating: {
      testMockClick: [],
      testMockPoint: [],
      testMockCollect: [],
      testMockComment: [],
    },
    otherRating: {
      testMockClick: [],
      testMockPoint: [],
      testMockCollect: [],
      testMockComment: [],
    },
    searchDate: [],
    bookcaseState: false
  },
  actions,
  mutations,
  getters: {
    show(state) {
      return state.user
    },
    getInit(state) {
      // console.log(localStorage.getItem('user'))
      return state
    },
    getNavDown: state => state.navDown,
    getLoginType: state => state.loginType,
    getLoginState: state => state.loginMiss,
    getLogoutState: state => state.logoutMiss,
    getCategory: state => state.category,
    getPageBook: state => state.book,
    getPage: state => state.numPage,
    getBookMeg: state => state.bookmeg,
    getPoint: state => state.point,
    getFontNum: state => state.fontNum,
    getPeople: state => state.people,
    getAverage: state => state.average,
    getChapterUser: state => state.chapterUser,
    bookLoading: state => state.bookLoad,
    getallRating: state => state.allRating,
    otherRating: state => state.otherRating,
    getSearch: state => state.searchDate,
    getBookcase: state => state.bookcaseState
  }
})

export default store



