<template>
  <div>
    <el-row>
      <div class="all">
        <el-col :lg="4" :md="6" :sm="8" :xs="8">
          <div v-if="router === 'index'" class="more" :class="{'active': Iture}" @mouseover="Button(true)"
               @mouseout="Button(false)"
               @click="down()">
            <div class="icon-更多"></div>
            <div class="text">作品分类</div>
          </div>
          <div v-if="router != 'index'" class="more" :class="{'active': Iture}" @mouseover="Button(true)"
               @mouseout="Button(false)"
               @click="returnIndex()">
            <div class="icon-undo2"></div>
            <div class="text">返回首页</div>
          </div>
        </el-col>
        <el-col :lg="17" :md="15" :sm="13" :xs="10">
          <div class="item">
            <ul>
              <li :class="{'active': index === mark}" v-for="(item,index) in navItem" @mouseover="changeNavbar(index)"
                  :key="index">
                <router-link :to="item.router">{{item.title}}</router-link>
              </li>
            </ul>
          </div>
        </el-col>
        <el-col :lg="3" :md="3" :sm="3" :xs="6">
          <div class="tel">
            <div class="text">手机端</div>
            <div class="icon-手机"></div>
          </div>
        </el-col>
      </div>
    </el-row>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        Iture: false,
        navItem: {
          '1': {
            title: '全部作品',
            router: '/all'
          },
          '2': {
            title: '排行',
            router: '/rating'
          },
          '3': {
            title: '聊天灌水区',
            router: '/chat'
          }
        },
        mark: 0,
        show: false
      }
    },
    computed: {
      router() {
        return this.$route.name
      }
    },
    methods: {
      Button(i) {
        this.Iture = i;
      },
      changeNavbar(index) {
        this.mark = index
      },
      down() {
        if (this.show) {
          this.$store.dispatch('setNavDown', false)
          this.show = false;
        } else {
          this.$store.dispatch('setNavDown', true)
          this.show = true;
        }
      },
      returnIndex() {
        this.$router.push('/')
      }
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  .el-row
    .all
      width: 100%;
      height: 40px;
      background: #3E3D43;
      .more
        width: 100%;
        min-width: 140px;
        height: 40px;
        transition: all 1s ease;
        cursor: pointer;
        .icon-更多, .icon-undo2
          float: left;
          line-height: 40px;
          margin: 0 10px 0 18px;
          color: #fff;
        .text
          float: left;
          line-height: 40px;
          color: #fff;
      .active
        background-color: #252428;
      .item
        width: 100%;
        height: 40px;
        overflow: hidden;
        li
          float: left;
          color: #fff;
          line-height: 40px;
          padding: 0 40px;
          transition: all .5s ease;
          cursor: pointer;
          a
            text-decoration: none;
            color: #fff;
        .active
          background-color: #252428;
          border-top: 2px solid #ED4259;
      .tel
        cursor: pointer;
        min-width: 100px;
        .text
          float: left;
          color: #fff;
          line-height: 40px;
        .icon-手机
          float: left;
          color: #fff;
          line-height: 40px;
          margin-left: 10px;
          font-size: 16px;
</style>



