<template>
  <transition name="fade">
    <div class="login" v-show="loginState">
      <div class="wrap">
        <div class="icon-叉号" @click="outLogin()"></div>
        <div class="title">登录</div>
        <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px"
                 class="demo-ruleForm">
          <el-form-item label="用户名:" prop="user">
            <el-input type="text" v-model="ruleForm2.user" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="密码:" prop="checkPass">
            <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm2')">登录</el-button>
            <el-button @click="goRegister">注册</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </transition>
</template>

<script>
  import {
    mapGetters
  } from 'vuex';

  export default {
    data() {
      var checkUser = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('用户名不能为空'));
        } else {
          return callback();
        }
      };
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          return callback(new Error('请输入密码'));
        } else {
          return callback();
        }
      };
      return {
        ruleForm2: {
          checkPass: '',
          user: ''
        },
        rules2: {
          checkPass: [
            {
              validator: validatePass, trigger: 'blur'
            }
          ],
          user: [
            {
              validator: checkUser, trigger: 'blur'
            }
          ]
        },
        state: false
      }
    },
    mounted() {
      if (this.$store.state.warning) {
        if (!this.state) {
          return;
        }
        this.$message.warning(this.$store.state.warning);
      }
    },
    computed: {
      ...mapGetters(["show"]),
      loginState() {
        this.state = this.$store.getters.getLoginState
        return this.$store.getters.getLoginState
      },
      loginType() {
        return this.$store.getters.getLoginType
      }
    },
    methods: {
      goRegister() {
        this.$router.push('/register')
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            return this.$store.dispatch('login', this.ruleForm2)
          } else {
            this.$message.warning('您输入有误！')
            return false;
          }
          this.$refs[formName].resetFields();
        });
      },
      outLogin() {
        this.$store.dispatch('setLoginMiss', false)
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
    @keyframes rotate-close
      0%
        transform: rotate(0)
      100%
        transform: rotate(360deg)
    .login
      position: absolute;
      top: 0;
      left: 0;
      z-index: 100;
      width: 100%;
      height: 100vh;
      background-color: rgba(7, 17, 27, 0.8);
      blur: 10px;
      .icon-叉号
        cursor: pointer;
        z-index: 120;
        position: absolute;
        right: 50px;
        top: 50px;
        font-size: 32px;
        color: #fff;
      .icon-叉号:hover
        animation: rotate-close .5s ease-in-out;
      .title
        font-size: 32px;
        color: #fff;
        text-align: center;
        margin-bottom: 30px;
        padding-top: 30px;
      .wrap
        width: 80%;
        height: auto;
        margin: 0 auto;
        label
          color: #fff;
        button
          margin-right: 10px;
          float: right;
    .fade-enter-active, .fade-leave-active
      transition: opacity 1s;
    .fade-enter, .fade-leave-to
      opacity: 0;
</style>



