<template>
  <div>
    <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
      <el-form-item label="用户名" prop="user">
        <el-input disabled type="text" v-model="ruleForm2.user" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="是否修改密码">
        <el-switch
          v-model="btnOn"
          active-color="#13ce66"
          inactive-color="#ff4949">
        </el-switch>
      </el-form-item>
      <el-form-item label="旧密码" prop="oldPass">
        <el-input :disabled="!btnOn" type="text" v-model="ruleForm2.oldPass" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="新密码" prop="pass">
        <el-input :disabled="!btnOn" type="password" v-model="ruleForm2.pass" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="确认新密码" prop="checkPass">
        <el-input :disabled="!btnOn" type="password" v-model="ruleForm2.checkPass" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="性别">
        <el-radio v-model="ruleForm2.sex" label="男">男</el-radio>
        <el-radio v-model="ruleForm2.sex" label="女">女</el-radio>
      </el-form-item>
      <el-form-item
        prop="mail"
        label="邮箱"
        :rules="[
      {  message: '请输入邮箱地址', trigger: 'blur' },
      { type: 'email', message: '请输入正确的邮箱地址', trigger: 'blur,change' }
    ]"
      >
        <el-input v-model="ruleForm2.mail"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm(ruleForm2)">修改</el-button>
        <el-button type="primary" @click="outForm()">取消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      var checkUser = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('用户名不能为空'));
        }
        setTimeout(() => {
          if (value.length < 4) {
            return callback(new Error('用户名必须超过4位'));
          } else {
            return callback();
          }
        }, 1000);
      };
      var validateOld = (rule, value, callback) => {
        if (value === '') {
          return callback(new Error('请输入旧密码'));
        } else {
          return callback();
        }
      }
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          return callback(new Error('请输入新密码'));
        } else {
          if (this.ruleForm2.checkPass !== '') {
            return this.$refs.ruleForm2.validateField('checkPass');
          }
          return callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          return callback(new Error('请再次输入新密码'));
        } else if (value !== this.ruleForm2.pass) {
          return callback(new Error('两次输入密码不一致!'));
        } else {
          return callback();
        }
      };
      return {
        ruleForm2: {
          pass: '',
          checkPass: '',
          user: '',
          mail: '',
          sex: '男',
          oldPass: ''
        },
        btnOn: false,
        rules2: {
          oldPass: [
            {
              validator: validateOld, trigger: 'blur'
            }
          ],
          pass: [
            {
              validator: validatePass, trigger: 'blur'
            }
          ],
          checkPass: [
            {
              validator: validatePass2, trigger: 'blur'
            }
          ],
          user: [
            {
              validator: checkUser, trigger: 'blur'
            }
          ]
        }
      };
    },
    created() {
      this.$store.dispatch('getFixUser')
      setTimeout(() => {
        if (this.$store.state.fixUser[0] != void 0) {
          this.ruleForm2 = this.$store.state.fixUser[0]
        } else {
          this.$message.warning('请录入数据再做次操作')
          this.$router.push('/userlist')
        }
      })
    },
    methods: {
      submitForm(data) {
        this.$http.post('/user/fixUserAdmin', data).then(res => {
          if (res.status == 200) {
            if (res.data.code === 0) {
              this.$store.dispatch('deleteFixUser')
              this.$message.warning(res.data.meg)
              this.$router.push('/userlist')
            } else {
              this.$message.warning(res.data.meg)
            }
          }
        })
      },
      outForm() {
        this.$store.dispatch('deleteFixUser')
        this.$router.push('/userlist')
      }
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
</style>



