<template>
  <div class="chapter">
    <chapter-select :nexts="next" :pres="pre" @itemchange="itemChange"></chapter-select>
    <div class="book" ref="book">
      <div class="extra">
        <el-color-picker v-model="colorSelect" size="medium" @change="colorChange"></el-color-picker>
        <div class="font" @click="expandFonts">
          <span>T</span>
          <transition name="Fontshow">
            <div class="expandFont" v-show="Tshow" @click="changeFont">
              <div class="font14 hover" data-font="14">较小字体</div>
              <div class="font16 hover" data-font="16">正常字体</div>
              <div class="font24 hover" data-font="24">稍大字体</div>
              <div class="font32 hover" data-font="32">超大字体</div>
            </div>
          </transition>
        </div>
        <el-color-picker v-model="fontColor" size="mini" @change="fontColorChange"></el-color-picker>
        <div class="text" @click="fontBack">还原</div>
      </div>
      <transition name="el-zoom-in-top">
        <div class="book_item" v-show="showBookItem">
          <li v-for="(items,index) in chapterMeg" :key="items._id">
            <router-link :to="'/getOneChapter/'+items._id">
              {{index + 1}}. {{items.title}}
            </router-link>
          </li>
        </div>
      </transition>
      <div class="chapter_num">第{{chapter.chapterNum}}章</div>
      <div class="chapter_title">{{chapter.title}}</div>
      <div class="chapter_text" ref="text" v-html="chapter.content"></div>
    </div>
    <chapter-select :nexts="next" :pres="pre"></chapter-select>
  </div>
</template>

<script>
  import chapterSelect from 'components/frontComponent/chapterSelect/chapterSelect'

  export default {
    data() {
      return {
        colorSelect: '#F6F1E7',
        fontColor: '#000',
        Tshow: false,
        chapter: [
          {
            chapterNum: '',
            title: '',
            name: '',
            content: '暂时没有该章节'
          }
        ],
        chaterSave: {},
        chaterUser: {
          fontState: '',
          set: ''
        },
        next: '',
        pre: '',
        bookid: '',
        showBookItem: false,
        chapterMeg: [],
        noChater: '暂时没有该书籍的章节'
      }
    },
    created() {
//      console.log(this.$route.params.id)
      this.changeChapter()
    },
    watch: {
      '$route'(to, from) {
        this.changeChapter()
      }
    },
    methods: {
      expandFonts() {
        this.Tshow = !this.Tshow
      },
      changeChapter() {
        this.$store.dispatch('chapterUser_Init')
        this.chaterSave = this.$store.getters.getChapterUser
        this.$http.get('/front/getOneChapter?id=' + this.$route.params.id).then(res => {
          if (res.status == 200) {
            if (res.data.code === 0) {
              this.next = res.data.next
              this.pre = res.data.pre
              this.chapter = res.data.data
              this.bookid = res.data.data.bookid
            }
          }
        }).then(() => {
          this.colorSelect = this.chaterSave.backgroundColor
          this.fontColor = this.chaterSave.fontColor
          for (var i = 0; i < this.$refs.text.getElementsByTagName('span').length; i++) {
            this.$refs.text.getElementsByTagName('span')[i].style.backgroundColor = this.chaterSave.backgroundColor
            this.$refs.text.getElementsByTagName('span')[i].style.fontSize = this.chaterSave.fontSize
            this.$refs.text.getElementsByTagName('span')[i].style.color = this.chaterSave.fontColor
          }
          this.$refs.book.style.background = this.chaterSave.backgroundColor
        })
      },
      colorChange() {
        this.$refs.book.style.background = this.colorSelect
        this.chaterUser.fontState = 'backgroundColor'
        this.chaterUser.set = this.colorSelect
        this.$store.dispatch('chapterUser_Set', this.chaterUser)
        for (var i = 0; i < this.$refs.text.getElementsByTagName('span').length; i++) {
          this.$refs.text.getElementsByTagName('span')[i].style.backgroundColor = this.colorSelect
        }
      },
      changeFont() {
        if (event.target.className.match('hover')) {
          var font = event.target.getAttribute('data-font') + 'px'
          this.chaterUser.fontState = 'fontSize'
          this.chaterUser.set = font
          this.$store.dispatch('chapterUser_Set', this.chaterUser)
          for (var i = 0; i < this.$refs.text.getElementsByTagName('span').length; i++) {
            this.$refs.text.getElementsByTagName('span')[i].style.fontSize = font
          }
        }
      },
      fontColorChange() {
        this.chaterUser.fontState = 'fontColor'
        this.chaterUser.set = this.fontColor
        this.$store.dispatch('chapterUser_Set', this.chaterUser)
        for (var i = 0; i < this.$refs.text.getElementsByTagName('span').length; i++) {
          this.$refs.text.getElementsByTagName('span')[i].style.color = this.fontColor
        }
      },
      fontBack() {
        var chapterUserBack = {
          fontColor: '#000',
          backgroundColor: '#F6F1E7',
          fontSize: '16px'
        }
        for (var i = 0; i < this.$refs.text.getElementsByTagName('span').length; i++) {
          this.$refs.text.getElementsByTagName('span')[i].style.backgroundColor = chapterUserBack.backgroundColor
          this.$refs.text.getElementsByTagName('span')[i].style.fontSize = chapterUserBack.fontSize
          this.$refs.text.getElementsByTagName('span')[i].style.color = chapterUserBack.fontColor
        }
        this.$refs.book.style.background = chapterUserBack.backgroundColor
        this.$store.dispatch('chapterUser_Set', chapterUserBack)
      },
      itemChange(val) {
        this.showBookItem = val
        if (this.showBookItem) {
          this.$http.get('/front/getChapter?bookid=' + this.bookid).then(res => {
            if (res.status === 200) {
              if (res.data.code === 0) {
                this.chapterMeg = res.data.data
              }
              if (res.data.code === 2) {
                this.noChater = res.data.data
              }
              if (res.data.code === 1) {
                this.$message.warning(res.data.meg)
              }
            } else {
              this.$message.warning('网络有错误')
            }
          })
        }
      }
    },
    components: {
      chapterSelect
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
    @keyframes fall-in
      0%
        transform: rotateX(-30deg);
      100%
        transform: rotateX(0);
    .chapter
      width: 100%;
      height: 100%;
      padding: 50px 0;
      .book
        position: relative;
        width: 70%;
        height: 100%;
        padding: 50px 0;
        margin: 0 auto;
        background: #F6F1E7;
        .book_item
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 500px;
          overflow: auto;
          background: #fff;
          & > li
            float: left;
            margin: 30px 20px 30px 20px;
            & > a
              text-decoration: none;
              color: #000;
            & > a:hover
              color: #aa1111;
        .chapter_num
          text-align: center;
          font-size: 32px;
        .chapter_title
          text-align: center;
          font-size: 24px;
          margin-top: 10px;
        .chapter_text
          width: 90%;
          height: 100%;
          margin: 0 auto;
          margin-top: 10px;
        .extra
          position: absolute;
          top: 0;
          left: -50px;
          width: 50px;
          height: auto;
          .font
            position: relative;
            width: 36px;
            height: 36px;
            margin: 10px 0;
            line-height: 30px;
            text-align: center;
            cursor: pointer;
            border: 1px solid #999;
            & > span
              width: 24px;
              height: 24px;
              display: block;
              margin: 0 auto;
              margin-top: 6px;
              overflow: hidden;
              border: 1px solid #999;
            .Fontshow-enter-active
              animation: fall-in .2s;
            .Fontshow-leave-active
              animation: fall-in .2s reverse;
            .expandFont
              position: absolute;
              top: 50px;
              left: -100px;
              width: 200px;
              height: auto;
              padding: 5px 0;
              background: #F6F1E7;
              border: 1px solid #ddd;
              line-height: 30px;
              z-index: 1008;
              transform-origin: center top 20px;
              .font14
                font-size: 14px;
              .font16
                font-size: 16px;
              .font24
                font-size: 24px;
              .font32
                font-size: 32px;
              .hover:hover
                background: #aa1111;
                color: #fff;
          .text
            width: 40px;
            height: 20px;
            text-align: center;
            line-height: 20px;
            margin-top: 10px;
            cursor: pointer;
            border: 1px solid #ddd;
          .text:hover
            background: #aa1111;
            color: #fff;
</style>



